import React from 'react'
import { toast } from "react-toastify";
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader'
import cssVar from 'views/mobileView/styles'
import db from 'libs/db'
import AdminServices from 'services/api/AdminServices';
import { BsGraphUp } from 'react-icons/bs';
import { Modal } from 'react-bootstrap';
import { setUserData } from 'redux/CompleteProfile/actions';
import { setCreateMintboxData } from 'redux/CreateMintbox/actions';

const RebalancingStrategy = (props) => {
    const [loading, setloading] = React.useState(false);
    const [rebalanceFrequency,setRebalanceFrequency] = React.useState(1)
    const [showHistoricalModal, setShowHistoricalModal] = React.useState(false)
    const options = [
        {key:1,text:"Every 3 months (recommended)"},
        {key:2,text:"Every 6 months"},
        {key:3,text:"Every 12 months"},
        {key:4,text:"Rebalance manually"}
    ]
    React.useEffect(() => {
        loadData();
    }, []);
    React.useEffect(() => {
        saveData(rebalanceFrequency)
    }, [rebalanceFrequency]);

    const loadData = async () => {
        try {
            db.get(db.model.createMintbox).then((u) => {
                if (u?.rebalanceFrequency) {
                    setRebalanceFrequency(u.rebalanceFrequency)
                }
            })
        } catch (error) {
            toast.error(error);
        }
    };

    const saveData = async (r) => {
        const dataFinal = {
            rebalanceFrequency: r
        };
        await db.get(db.model.createMintbox).then((u) => {
            if (u) { Object.assign(u, dataFinal) }
            else { u = dataFinal }
            setCreateMintboxData(u);
        })
    }

    const Footer = () => {
        return (
            <>
                <div style={{ position: "fixed", bottom: 0,width:"100%" }}>
                <div onClick={()=>setShowHistoricalModal(true)} style={{ fontSize: "14px", fontWeight: '400', color: "#00A6A4", textAlign: "center", borderTop: "1px solid #e5e5e5", padding: "2% 4%", background: "#FFFFFF" }}>
                        Historical performance <BsGraphUp size={16} style={{margin:"5px"}}/>
                    </div>
                    <div style={{ fontSize: "14px", fontWeight: '400', color: "6c6c6c", textAlign: "center", borderTop: "1px solid #e5e5e5", padding: "2% 4%", background: "#FFFFFF" }}>
                        Minimum Investment <b>RS 12344</b>
                    </div>
                    <div style={{ background: "#FAFAFA", display: "flex",borderTop: "1px solid #e5e5e5" }}>
                        <div style={{ flex: 1, padding: "4%" }}>
                            <button
                                style={{
                                    borderRadius: 4,
                                    fontSize: "14px",
                                    backgroundColor: cssVar.getPropertyValue("--brandSecondary"),
                                    color: "#FFFFFF",
                                    height: "44px",
                                    width: "100%",
                                    border: "none"
                                }}
                                onClick={() => { props.history.push('/build-your-own-mintbox/name-mintbox') }}
                            >
                                Finish building your mintbox
                            </button>
                        </div>
                    </div>
                </div>
            </>
        )
    }
    const HistoricalModal = () => {
        return (
            <Modal
                className="bottom-drawer"
                show={showHistoricalModal}
                backdrop="static"
                onHide={() => setShowHistoricalModal(false)}
                keyboard={false}
            >
                <Modal.Body>
                    <div style={{fontSize:"12px",fontWeight:"400",color:"#6C6C6C"}}>
                        <b>Disclaimer:</b> Past performance is not an assurance of future
                        performance. The calculations do not take into account
                        taxation, mintbox fee and other charges like STT.
                    </div>
                    <div style={{fontSize:"12px",fontWeight:"400",color:"#6C6C6C"}}><b>Note:</b> We have assumed a 3 month rebalancing frequency</div>
                </Modal.Body>
                <Modal.Footer>
                    <button className="close" onClick={() => setShowHistoricalModal(false)}>
                        Close
                    </button>
                </Modal.Footer>
            </Modal>
        )
    }
    return (
        <div>
            <MainHeader
                header="Build your own mintbox"
                right="help"
                screenType="Invest"
                navigation={props.history}
            />
            <div className="containerData px-0 pb-4" style={{marginBottom:"120px"}}>
                    <div style={{ margin: "4%" }}>
                        <div style={{ width: "100%", height: "165px", background: "rgb(244, 248, 255)", border: "1px solid rgb(229, 229, 229)", borderRadius: "6px" }}></div>
                        <p style={{ marginTop: "4%",fontSize:"16px",fontWeight:"500" }}>How often would you like to rebalance your mintbox? 
                        <span style={{color:cssVar.getPropertyValue("--brandSecondary"), fontSize:"14px"}}> Watch how rebalancing works &gt;</span></p>
                        {
                            options.map((option)=>{
                                return(
                                    <div className="radio-item" key={option.key} style={{display:"block"}}>
                                        <input type="radio" id={option.key} name="rebalanceFrequency" className="textNormal"
                                            onChange={(e) => {setRebalanceFrequency(parseInt(e.target.value))}} value={option.key}
                                            checked={option.key===rebalanceFrequency}/>
                                        <label className="textNormal radio-label" htmlFor={option.key}>{option.text}</label>
                                    </div>
                                )
                            })
                        }
                    </div>
                    <Footer />
                    <HistoricalModal/>
            </div>
        </div>
    )
}

export default RebalancingStrategy
