import React from "react";
import cssVar from "views/mobileView/styles";
import { MdPictureInPictureAlt } from "react-icons/md"
import { Chip } from '@mui/material';
import { ManWithCalculator, MintboxSmallCube } from "assets/SVG";
import ReadMoreText from "./ReadMoreText";
import { AiFillRightCircle } from 'react-icons/ai'
export default function SliderComponent({ navigation, type, component: { header = "", description = "", content, right } }) {
    const renderRightSide = () => {
        switch (right) {
            case 'See all':
                return (<span style={{
                    fontSize: cssVar.getPropertyValue('--font-helper'),
                    padding: "2px 10px 2px 10px",
                    border: `1px solid #294C88`,
                    borderRadius: "20px",
                    backgroundColor: "#F0F9FF",
                    color: '#294C88'
                }}>
                    See all &gt;
                </span>);
            case 'All':
                return (<Chip>All Funds</Chip>);
            default:
                return <span style={{ padding: 5 }}></span>;
        }
    };
    const hci_card = (slide) => {
        return (<div
            key={slide.key}
            style={{
                marginLeft: "4%",
                padding: "6% 0 0 4%",
                border: "1px solid " + cssVar.getPropertyValue("--divider"),
                borderRadius: 10,
                backgroundColor: slide.backgroundColor,
                display: "inline-block",
                width: "85%",
                minheight: "120px",
                whiteSpace: "normal",
                flex: 1
            }}
        >
            <div style={{ color: slide.titleColor, fontSize: cssVar.getPropertyValue('--font-normal'), fontWeight: "500", marginBottom: "4%" }}>
                {slide.title}
            </div>
            <div style={{ display: "flex" }}>
                <div style={{ flex: "7" }}>
                    <p style={{ fontSize: cssVar.getPropertyValue('--font-helper'), fontWeight: "400", color: cssVar.getPropertyValue("--textLight"), marginBottom: 0 }}>
                        {slide.text}
                    </p>
                </div>
                <div
                    style={{
                        flex: "3"
                    }}
                >
                    {slide.icon}
                </div>
            </div>
        </div>)
    }
    const box_card = (slide) => {
        let backgroundColor = '';
        let buttonColor = '';
        switch (slide.ColorScheme) {
            case "BLUE": {
                backgroundColor = "#F4F8FF"
                buttonColor = "#294C88"
                break;
            }
            case "GREEN": {
                backgroundColor = "#C5EED140"
                buttonColor = "#00A6A4"
                break;
            }
            case "YELLOW": {
                backgroundColor = "#FAF8F5"
                buttonColor = "#294C88"
                break;
            }
            case "PINK": {
                backgroundColor = "#F8EBEC"
                buttonColor = "#294C88"
                break;
            }
            case "LIGHTBLUE": {
                backgroundColor = "#C3E7FF40"
                buttonColor = "#294C88"
                break;
            }
        }
        return (<div
            key={slide.UID}
            style={{
                marginLeft: "4%",
                padding: "3%",
                border: "1px solid " + cssVar.getPropertyValue("--divider"),
                borderRadius: 10,
                backgroundColor: backgroundColor,
                display: "inline-block",
                width: "85%",
                minheight: "165px",
                whiteSpace: "normal",
                flex: 1
            }}
        >
            <div style={{ color: "#333333", fontSize: "16px", fontWeight: "500", marginBottom: "4%" }}>
                {slide.SubTitle}
            </div>
            <p style={{ fontSize: cssVar.getPropertyValue('--font-helper'), fontWeight: "400", color: cssVar.getPropertyValue("--textLight"), marginBottom: 0, width: "90%" }}>
                {slide.Description}
            </p>
            <div style={{ display: "flex" }}>
                <div style={{ flex: 7, display: "flex", alignItems: "flex-end" }}>
                    <button
                        style={{
                            borderWidth: "1px",
                            borderRadius: 8,
                            borderColor: buttonColor,
                            fontSize: "14px",
                            backgroundColor: slide.IsBorderedButton ? "#FFFFFF" : buttonColor,
                            color: slide.IsBorderedButton ? buttonColor : "#FFFFFF",
                            width: "75%",
                            height: "44px",
                        }}
                        onClick={() => {
                            slide.onClick && slide.onClick();
                            slide.ActionTarget !== '' && navigation.push(slide.ActionTarget)
                        }}
                    >
                        {slide.ActionText}
                    </button>
                </div>
                <div
                    style={{
                        flex: 3,
                        display: "flex",
                        alignItems: "flex-end",
                    }}
                >
                    <img src={ManWithCalculator} alt="" />
                </div>
            </div>
        </div>)
    }
    const recently_viewed = (slide) => {
        return (<div
            key={slide.UID}
            style={{
                marginLeft: "4%",
                padding: "4% 2%",
                border: "1px solid " + cssVar.getPropertyValue("--divider"),
                borderRadius: 6,
                backgroundColor: "#FAFAFA",
                display: "inline-block",
                width: "85%",
                minHeight: "120px",
                whiteSpace: "normal",
                flex: 1
            }}
        >
            <img style={{ padding: "1%", border: "1px solid " + cssVar.getPropertyValue("--divider"), backgroundColor: "#F2FBFB", borderRadius: 5, marginBottom: "2%" }} src={MintboxSmallCube} />
            <div style={{ display: "flex" }}>

                <div style={{ flex: 2 }}>
                    <div style={{ color: "#333333", fontSize: "14px", fontWeight: "500", marginBottom: "4%", width: "90%" }}>
                        {slide.AssetType === 1 ? slide.ETFName : ""}
                        {slide.AssetType === 2 ? slide.FundName : ""}
                        {slide.AssetType === 3 ? slide.MintboxName : ""}
                    </div>
                    <p style={{ fontSize:"12px", fontWeight: "400", color: cssVar.getPropertyValue("--textLight"), marginBottom: 0, width: "90%" }}>
                        {slide.AssetType === 3 ? <>{slide.Risk + " mintbox"}<br /> {"Ideal for " + slide.Duration} </> : slide.AssetClass}
                    </p>
                </div>
                <div style={{ flex: 1 }}>
                    <div style={{ fontSize: cssVar.getPropertyValue('--font-helper'), fontWeight: "400", color: cssVar.getPropertyValue("--textLight") }}>3 year return</div>
                    <div style={{ fontSize: cssVar.getPropertyValue('16px'), fontWeight: "500", color: cssVar.getPropertyValue("--primary") }}>{slide.XIRR + " %"}</div>
                </div>
            </div>
        </div>)
    }
    const video_card = (slide) => {
        return (
            <a key={slide.UID} href={slide.URL} target="_blank">
                <div
                    style={{
                        marginLeft: "4%",
                        borderRadius: 10,
                        display: "inline-block",
                        width: "85%",
                        whiteSpace: "normal"
                    }}>
                    <div style={{ position: "relative" }}>
                        <img
                            src={`https://img.youtube.com/vi/${slide.URL.slice(-11)}/maxresdefault.jpg`}
                            style={{
                                paddingVertical: 10,
                                width: "100%",
                                borderRadius: 6,
                            }} />
                        <img
                            src='https://cdn4.iconfinder.com/data/icons/logos-and-brands/512/395_Youtube_logo-512.png'
                            style={{
                                position: 'absolute',
                                width: "40px",
                                bottom: 8,
                                right: 16,
                            }} />
                    </div>
                </div>
            </a>
        )
    }
    const blog_card = (slide) => {
        return (
            <div
                key={slide.UID}
                onClick={() => { navigation.push(slide.ActionTarget) }}
                style={{
                    border: "1px solid #9eadbd4D",
                    backgroundColor: "#fff",
                    width: "85%",
                    marginLeft: "4%",
                    borderRadius: 6,
                    display: "inline-block",
                    whiteSpace: "normal",
                    overflow: "hidden"
                }}>
                <img
                    src='https://blog.feedspot.com/wp-content/uploads/2016/01/Personal-Finance-Blogs.jpg'
                    style={{
                        paddingVertical: 10,
                        height: "12%",
                        width: "100%",
                    }} />
                <div style={{ color: cssVar.getPropertyValue('--brandSecondary'), fontSize: "16px", fontWeight: "500", margin: "2%" }}>
                    {slide.SubTitle}
                </div>
                <p style={{ fontSize: cssVar.getPropertyValue('--font-helper'), fontWeight: "400", color: cssVar.getPropertyValue("--textLight"), margin: "2%" }}>
                    {slide.Description}
                </p>
            </div>
        );
    }
    const small_box = (slide) => (

        <div
            key={slide.UID}
            style={{
                borderRadius: 6, border: "1px solid " + cssVar.getPropertyValue("--divider"),
                backgroundColor: slide.bgColor, padding: 16, width: 120, height: 128,
                marginLeft: "4%", display: "inline-block", whiteSpace: "normal"
            }}
            onClick={() => slide.onClick()}
        >
            <div>
                <div style={{ fontSize: 14 }}>{slide.heading}</div>
                <AiFillRightCircle
                    size={20}
                    style={{
                        marginTop: 20,
                        color: slide.btnColor,
                        alignSelf: "flex-start",
                    }} />
            </div>
        </div>

    )
    return (
        <div style={{ margin: "4% 0" }}>
            {header ?
                <div style={{ margin: "1% 4%", display: 'flex', flex: 1, flexDirection: 'row', justifyContent: 'space-between' }}>
                    <span style={{ fontSize: cssVar.getPropertyValue('--font-head'), fontWeight: 500, color: "#333333" }}>{header}</span>
                    <span>
                        {renderRightSide()}
                    </span>
                </div> : ""}
            {description ?
                <p style={{ margin: "0 4%", color: "#6C6C6C" }}>
                    <ReadMoreText text={description} />
                </p>
                : ""}
            <div style={{ marginTop: "3%", overflowX: "auto", whiteSpace: "nowrap", scrollbarWidth: "none" }}>
                {type === "hci" && content.map(slide => (
                    hci_card(slide)
                ))}
                {type === "videoView" && content.map(slide => (
                    video_card(slide)
                ))}
                {type === "blogView" && content.map(slide => (
                    blog_card(slide)
                ))}
                {type === "boxCard" && content.map(slide => (
                    box_card(slide)
                ))}
                {type === "recentlyViewed" && content.map(slide => (
                    recently_viewed(slide)
                ))}
                {type === "smallBox" && content.map(slide => (
                    small_box(slide)
                ))}
            </div>
        </div>
    )
};

