import React from 'react'
import { toast } from "react-toastify";
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader'
import cssVar from 'views/mobileView/styles'
import db from 'libs/db'
import AdminServices from 'services/api/AdminServices';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import { setCreateMintboxData } from 'redux/CreateMintbox/actions';

const NameMintbox = (props) => {
   
    const [mintboxName,setMintboxName] = React.useState('')
    const [selectedETFs,setSelectedETFs] = React.useState([])
    const [rebalanceFrequency,setRebalanceFrequency] = React.useState('')
    
    const [user, setUser] = React.useState({});
    const [loading, setloading] = React.useState(false);
    
    React.useEffect(async () => {
        await loadData();
    }, []);

    React.useEffect(() => {
        saveData(mintboxName)
    }, [mintboxName]);

    const loadData = async () => {
        try {
            await db.get(db.model.user, true).then((p) => {
                setUser(p.userData);
                console.log("user: ", p.userData)
            });
            await db.get(db.model.createMintbox).then((u) => {
                if (u?.selectedETFs) {
                    setSelectedETFs(u.selectedETFs);
                }
            })
            await db.get(db.model.createMintbox).then((u) => {
                if (u?.rebalanceFrequency) {
                    setRebalanceFrequency(u.rebalanceFrequency)
                }
            })
            await db.get(db.model.createMintbox).then((u) => {
                if (u?.mintboxName) {
                    setMintboxName(u.mintboxName)
                }
            })
        } catch (error) {
            toast.error(error);
        }
    };
    const saveData = async (name) => {
        const dataFinal = {
            mintboxName: name
        };
        await db.get(db.model.createMintbox).then((u) => {
            if (u) { Object.assign(u, dataFinal) }
            else { u = dataFinal }
            setCreateMintboxData(u);
        })
    }
    const onNextPress = async ()=> {
        const x = selectedETFs.map((fund) =>{
            return {
                fundCode: Number(fund.AMFICode),
                indexId: 1,
                assetId: Number(fund.AssetClassId),
                weightage: Number(fund.weight)
            }
        })
        const final = {
            clientId: user.id,
            name: mintboxName,
            description: '',
            risk: 1,
            duration: 34,
            minInvestment: 23,
            xirr: 23,
            constituents: x,
            rebalanceFrequency: rebalanceFrequency,
            videoLink: "et sit d",
            fee: "free"
        }
        console.log("CREATE MINTBOX API",final)
        let res = await AdminServices.createMintbox(final).then((res) => JSON.stringify(res));
        res = await JSON.parse(res);
        if (res.type === true) {
            if(res.data.Result === 'Success'){
                console.log("Mintbox created",res.data)
                db.remove(db.model.createMintbox)
            }
            else{
                toast.error(res.data)
            }
        } else {
            toast.error()
        }
    }
    const Footer = () => {
        return (
            <>
                <div style={{ position: "fixed", bottom: 0,width:"100%" }}>
                    <div style={{ fontSize: "14px", fontWeight: '400', color: "6c6c6c", textAlign: "center", borderTop: "1px solid #e5e5e5", padding: "2% 4%", background: "#FFFFFF" }}>
                        Minimum Investment <b>RS 12344</b>
                    </div>
                    <div style={{ background: "#FAFAFA", display: "flex",borderTop: "1px solid #e5e5e5" }}>
                        <div style={{ flex: 1, padding: "4%", paddingRight: 0 }}>
                            <div style={{ fontSize: "14px", fontWeight: "400", marginBottom: "2%" }}>
                                Track this mintbox
                                <span style={{display:"block",fontSize:"12px",fontWeight:"500",color:"#00A6A4"}}>Add to watchlist &gt;</span>
                            </div>
                        </div>
                        <div style={{ flex: 1, padding: "4%" }}>
                            <button
                                style={{
                                    borderRadius: 4,
                                    fontSize: "14px",
                                    backgroundColor: cssVar.getPropertyValue("--brandSecondary"),
                                    color: "#FFFFFF",
                                    height: "44px",
                                    width: "100%",
                                    border: "none"
                                }}
                                onClick={() => {onNextPress()}}
                            >
                                Invest Now
                            </button>
                        </div>
                    </div>
                </div>
            </>
        )
    }
    return (
        <div>
            <MainHeader
                header="Build your own mintbox"
                right="help"
                screenType="Invest"
                navigation={props.history}
            />
            <div className="containerData px-0 pb-4" style={{marginBottom:"120px"}}>
                    <div style={{ margin: "4%" }}>
                        <div style={{ width: "100%", height: "165px", background: "rgb(244, 248, 255)", border: "1px solid rgb(229, 229, 229)", borderRadius: "6px" }}></div>
                        <p style={{ marginTop: "4%",fontSize:"16px",fontWeight:"500" }}>Name your mintbox</p>
                        <FloatingTextInput type="text" autoFocus placeholder='Max 20 characters' maxLength={20} value={mintboxName}
                            onChange={e=>setMintboxName(e.target.value) }/>
                    </div>
                    <Footer />
            </div>
        </div>
    )
}

export default NameMintbox
