import React from 'react';
import { Button, Modal, Spinner } from 'react-bootstrap';
import { toast } from 'react-toastify';
import { FiInfo } from 'react-icons/fi';
import colorsFinal from 'theme/colors';

import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import { emailValidator } from 'libs/validators';
import AdminServices from 'services/api/AdminServices';
import db from 'libs/db';
import { setUserData } from 'redux/CompleteProfile/actions';
import DOBInputMask from 'views/mobileView/components/atoms/DOBInputMask';
import cssVar from 'views/mobileView/styles';
import CustomDropDown from 'views/mobileView/components/molecules/DropDown/CustomDropDown';
import { dropdownCustom } from 'views/mobileView/components/atoms/DataCustomizer';
import BottomBackNextBtn from 'views/mobileView/components/atoms/BottomBackNextBtn';
import { Chip, Stack } from '@mui/material';
import Accordion from 'views/mobileView/components/atoms/Accordion';
import { getDDMMYYY } from 'views/mobileView/components/atoms/DOBFormatter';
import NumericInput from 'views/mobileView/components/atoms/NumericInput';

export default function CompleteProfilePage2(props) {

    const [jointyes, setjointYes] = React.useState('no');
    const [addnomine, setaddnomine] = React.useState('no');
    const [nomDataArr, setnomiDataArr] = React.useState([{
        noName: '',
        relation: '',
        guardName: '',
        dob: '',
        nomAllocate: 0
    }]);
    const [joint, setJoint] = React.useState('joint');
    const [name, setname] = React.useState('');
    const [dob, setdob] = React.useState('');
    const [dobv, setdobv] = React.useState('');
    const [income, setincome] = React.useState('');
    const [wealth, setwealth] = React.useState('');
    const [occupation, setoccupation] = React.useState('');
    const [activeNum, setactiveNum] = React.useState(0);
    const [nomloading, setnmloading] = React.useState(false);
    const [nomAlloList, setnomAlloList] = React.useState([]);

    const [loading, setloading] = React.useState(true);
    const [helperType, sethelperType] = React.useState('');
    const [helperColor, sethelperColor] = React.useState('');
    const [helperText, sethelperText] = React.useState('');
    const [dshow, setdshow] = React.useState(false);
    const [ddata, setddata] = React.useState([]);
    const [nextdisabled, setnextDisabled] = React.useState(true);

    const chipset = [
        { "key": "a", "name": "Father" },
        { "key": "b", "name": "Mother" },
        { "key": "c", "name": "Spouse" },
        { "key": "d", "name": "Son" },
        { "key": "e", "name": "Daughter" },
    ]
    const getData = async () => {

    };
    const isValidAllo = () => {
        var sum = 0;
        nomAlloList.map((data, index) => {
            sum += parseInt(data.nomAllocate);
        })
        if (sum !== 100) {
            setnextDisabled(true)
        } else {
            setnextDisabled(false)
        }
    }
    function getAge(dateString) {
        dateString = dateString.split(" / ").reverse().join("/");
        var today = new Date();
        var birthDate = new Date(dateString);
        var age = today.getFullYear() - birthDate.getFullYear();
        var m = today.getMonth() - birthDate.getMonth();
        if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
            age--;
        }
        return age;
    }
    const checkValidation = () => {
        if (addnomine === 'no') {
            setnextDisabled(false);
        } else if (addnomine !== 'no') {
            if (nomDataArr.length === 0) {
                setnextDisabled(true);
            }
            for (var i = 0; i < nomDataArr.length; i++) {
                console.log("h1", nomDataArr[i].noName)
                if (nomDataArr[i].noName !== '' && nomDataArr[i].relation !== '' && nomDataArr[i].dob !== '') {
                    if (getAge(nomDataArr[i].dob) < 19) {
                        if (nomDataArr[i].guardName !== '') {
                            setnextDisabled(false);

                        } else {
                            setnextDisabled(true);
                            break;
                        }
                    } else {
                        setnextDisabled(false);
                    }
                } else {
                    setnextDisabled(true);
                    break;
                }
            }
        }
    };
    const changeData = (index, text) => {
        nomAlloList[index].nomAllocate = text
        setnomAlloList(nomAlloList)
        isValidAllo()
        setnmloading(true)
    }
    const dataup = () => {
        var a = nomDataArr
        if (a.length === 1) {
            a[0].nomAllocate = 100
        } else if (a.length === 2) {
            a[0].nomAllocate = 50
            a[1].nomAllocate = 50
        } else if (a.length === 3) {
            a[0].nomAllocate = 34
            a[1].nomAllocate = 33
            a[2].nomAllocate = 33
        }
        return a
    }

    React.useEffect(() => {
        setnmloading(false)
        checkValidation()
    }, [nomloading])

    const addNominee = async () => {

        let a = nomDataArr;
        if (a.length <= 2) {
            a.push({
                noName: "",
                relation: "",
                guardName: "",
                dob: '',
                nomAllocate: 0
            })
            await setnomiDataArr(a)
            setactiveNum(nomDataArr.length - 1)
            setnmloading(true)
        }
    }

    const delNominee = (index) => {
        var a = nomDataArr;
        a.splice(index, 1)
        setnomiDataArr(a)
        setnmloading(true)
    }

    const nextValidate = () => {
        // if ((value === '0' || value === '2') && showdata) {
        //     return true;
        // } if (value === '1') {
        //     return true;
        // }
        return true;
    };

    const onNextPress = () => {
        const dataFinal = {
            joint: {
                jointData: {
                    jointvalue: jointyes,
                    jointUser: {
                        name,
                        dob,
                        fatca: {
                            income,
                            wealth,
                            occupation
                        },
                        accountType: joint
                    },
                    nominevalue: addnomine,
                    nomineData: dataup()
                }
            }
        };

        db.get(db.model.userData).then((u) => {
            if (u) {
                Object.assign(u, dataFinal);
            } else {
                u = dataFinal
            }
            setUserData(u).then(() => {
                props.history.push({
                    pathname: '/admin/client/complete-pro/add-a-client/3',
                    state: props.location.state
                })
            })
        });
    };
    React.useEffect(() => {
        db.get(db.model.userData).then((u) => {
            if (true) {
                getData().then(() => {
                    setloading(false);
                });
            }
            else { setloading(false); }
            if (u) {
                const r1 = u.joint;
                // setoccupationList(u.fatca.occupationList || []);
                // setincomeList(u.fatca.incomeList || []);
                // setwealthList(u.fatca.wealthList || []);
                if (r1) {
                    const r2 = u.joint.jointData;
                    if (r2.nominevalue === 'yes') {
                        setaddnomine(r2.nominevalue || '')
                        setnomiDataArr(r2.nomineData || [])
                        setnomAlloList(r2.nomineData)
                    } else {
                        setaddnomine(r2.nominevalue || '');
                    }
                }
                setloading(false);
            }
        });
    }, []);

    const NomineDetails = (data) => {

        console.log(data)

        const [noName, setnoName] = React.useState(data.noName);
        const [relation, setrelation] = React.useState(data.relation);
        const [guardName, setGuardName] = React.useState(data.guardName);
        const [dob, setdob] = React.useState(data.dob);

        return <Accordion index={data.index}
            active={data.index === activeNum ? true : false}
            dividerStyle={{ borderBottomWidth: 0.1 }}
            title={<span className="textNormal">Nominee {data.index + 1} </span>}
            secTitle={<span style={{ color: colorsFinal.placeholder }}>{<br></br>}{nomDataArr[data.index].noName} </span>}
            left={null}
            // right={() => {
            // 	return <span className="textNormal" style={{ color: cssVar.getPropertyValue('--red') }} onClick={() => {
            // 		delNominee(data.index);
            // 	}}>Remove</span>
            // }}
            right={() =>
                data.index === activeNum ? (<span className="textNormal" style={{ color: cssVar.getPropertyValue('--red') }} onClick={() => {
                    delNominee(data.index);
                }}>Remove</span>) : (<span className="textNormal" style={{ color: cssVar.getPropertyValue('--primary') }} onClick={() => {
                    setactiveNum(data.index)
                }}>Edit</span>)
            }
            updateExpand={(i, val) => {
                console.log(i, val)
                if (val) {
                    setactiveNum(i)
                } else {
                    setactiveNum('')
                }
            }}
        >
            <div className='row'>
                <div className="col-6">
                    <FloatingTextInput
                        type="text"
                        id="full name"
                        label="Full name"
                        height="2.5rem"
                        value={noName}
                        // onFocus={() => checkValidation()}
                        // onBlur={() => checkValidation()}
                        onChange={(text) => {
                            var aa = nomDataArr
                            aa[data.index].noName = text.target.value
                            setnomiDataArr(aa)
                            setnoName(text.target.value)
                            console.log(text.target.value)
                        }}
                    />
                </div>
                <div className="col-6">
                    <FloatingTextInput
                        value={relation}
                        type="text"
                        autoCapitalize="words"
                        id="relation"
                        label="Relationship with investor"
                        height="2.5rem"
                        onBlur={() => checkValidation()}
                        onChange={(text) => {
                            var aa = nomDataArr
                            aa[data.index].relation = text.target.value
                            setnomiDataArr(aa)
                            setrelation(text.target.value)
                        }}
                    />

                    <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
                        {chipset.map((item) => {
                            return (
                                <Stack direction="row" spacing={1}>
                                    <Chip
                                        // size="small"
                                        className="chip-a"
                                        // style={{ fontSize: cssVar.getPropertyValue('--font-helper') }}
                                        label={item.name}
                                        variant="outlined"
                                        onClick={() => {
                                            var aa = nomDataArr
                                            aa[data.index].relation = item.name
                                            setnomiDataArr(aa)
                                            setrelation(item.name)
                                        }} />
                                </Stack>
                            )
                        })}
                    </div>
                </div>
            </div>
            <div className='row'>
                <div className='col-6'>
                    <DOBInputMask
                        dateCheck
                        defaultVal={dob}
                        dobof={props.location.state.selection}
                        minAge={0}
                        maxAge={200}
                        height="2.5rem"
                        dateValidation={(dobvv) => setdobv(dobvv)}
                        setDOB={(dobb) => {
                            // console.log(dob);
                            var aa = nomDataArr
                            aa[data.index].dob = dobb
                            setnomiDataArr(aa)
                            setdob(dobb);
                        }}
                        onBlur={() => checkValidation()}
                    />
                </div>
                <div className='col-6'>
                    {getAge(dob) < 19 ? (
                        <FloatingTextInput
                            type="text"
                            id="guard name"
                            autoCapitalize="words"
                            label="Guardian name"
                            onBlur={() => checkValidation()}
                            value={guardName}
                        height="2.5rem"
                        onChange={(text) => {
                                var aa = nomDataArr
                                aa[data.index].guardName = text.target.value
                                setnomiDataArr(aa)
                                setGuardName(text.target.value)
                                console.log(text.target.value)
                            }}
                        />
                    ) : null}
                </div>
            </div>
        </Accordion>
    }
    if (loading) {
        return (
            <div className="center-screen">
                <Spinner
                    as="span"
                    animation="grow"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                />
            </div>
        );
    } else {
        return <div className="container">
            <Modal
                show={dshow}
                onHide={() => setdshow(!dshow)}
                backdrop="static"
                keyboard={false}
                size="sm"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Body>
                    <p className="textHead">{ddata[0]}</p>
                    <p className="textNormal">{ddata[1]}</p>
                </Modal.Body>
                <Button variant="light" style={{ color: colorsFinal.brandPrimary }} onClick={() => setdshow(!dshow)}>
                    Okay
                </Button>
            </Modal>
            <div className="floating-form">
                <div className="row">
                    <div className="col-6">
                        <div>
                            <span className="textHelper ">Do you wish to add a nominee?</span>
                            <FiInfo
                                className="mb-1 mx-2"
                                size={15}
                                color={cssVar.getPropertyValue("--primary")}
                                onClick={() => {
                                    setddata(["", "It is strongly recommended that you add a nominee. All your investments will be transferred to the nominee after your death and will help settle claims faster."]);
                                    setdshow(true);
                                }}
                            />
                        </div>
                        <div className="radio-item">
                            <input
                                type="radio"
                                id="ritem"
                                name="ritem"
                                value="yes"
                                onChange={(val) => {
                                    setaddnomine(val.target.value)
                                    console.log(val.target.value)
                                }}
                                className="textNormal"
                                checked={addnomine === "yes"}
                            />
                            <label className="textNormal radio-label" htmlFor="ritem">Yes</label>
                        </div>
                        <div className="radio-item">
                            <input
                                type="radio"
                                id="ritemb"
                                name="ritem"
                                value="no"
                                onChange={(val) => {
                                    setaddnomine(val.target.value)
                                    console.log(val.target.value)
                                }}
                                className="textNormal"
                                checked={addnomine === "no"}
                            />
                            <label className="textNormal radio-label" htmlFor="ritemb">No</label>
                        </div>
                    </div>
                        {addnomine === 'yes' ? (
                            <div style={{ display: "flex", flexDirection: "column" }}>
                                {nomloading ? null :
                                    nomDataArr.map((data, index) => {
                                        return <NomineDetails {...data} index={index} />
                                    })}{<br />}

                                {nomDataArr.length > 2 ? (null) : (
                                    <>
                                        <span style={{ fontSize: cssVar.getPropertyValue('--font-normal'), color: cssVar.getPropertyValue('--primary') }} onClick={() => { addNominee(); }}>Add {nomDataArr.length === 0 ? "" : "another "}nominee</span>
                                        <span style={{ fontFamily: 'DMSans-Regular', fontSize: "10px", color: cssVar.getPropertyValue('--textLight'), marginBottom: "20px" }} >You can add up to 3 nominees</span>
                                    </>
                                )}
                            </div>
                        ) : null}
                    
                </div>
                <div className="row">
                    <p className="textHelper">Please specify nominee allocation</p>
                    {
                        nomAlloList.map((data, index,) => {
                            return (
                                <div className="col-4 my-2" style={{
                                    display: 'flex',
                                    flexDirection: 'row',
                                }}>
                                    <div style={{
                                        display: 'flex',
                                        flexDirection: 'column',
                                        flex: 1,
                                        justifyContent: "center"
                                    }} >
                                        <span style={{ marginBottom: 0 }}>{data.noName}</span>
                                        <span style={{ color: cssVar.getPropertyValue('--red') }} onClick={() => { delNominee(nomAlloList.length - 1); }}>Remove</span>
                                    </div>
                                    <div style={{ display: 'flex', flex: 1, justifyContent: "center", alignItems: "center" }}>
                                        <NumericInput
                                            onLimitReached={(isMax, msg) => console.log(isMax, msg)}
                                            id={"allo" + index}
                                            minValue={1}
                                            maxValue={100}
                                            value={data.nomAllocate}
                                            onChange={value => changeData(index, value)}
                                        />
                                    </div>
                                </div>
                            )
                        })
                    }
                    {nextdisabled ? <span style={{
                        marginTop: "30px",
                        padding: "11px",
                        backgroundColor: cssVar.getPropertyValue('--red') + '33',
                        textAlign: 'center',
                        color: cssVar.getPropertyValue('--red')
                    }}>Sum should be 100</span> : null}
                </div>

            </div>
            <div>
                <Button
                    variant="primary"
                    className="btn-footer"
                    style={{ opacity: nextValidate() ? 1 : 0.4 }}
                    onClick={nextValidate()
                        ? () => onNextPress()
                        : () => { }}
                >
                    Next &#8594;
                </Button>
            </div>
        </div>
    }
}
