import React from 'react';
import { Button, Spinner } from 'react-bootstrap';
import { AiOutlineRight } from 'react-icons/ai';
import { toast } from 'react-toastify';
import colorsFinal from 'theme/colors';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import AdminServices from '../../../../../services/api/AdminServices';
import CountDown from '../../../components/atoms/CountDown';
import AuthHeader from '../../../components/molecules/Headers/AuthHeader';
import { browserName, deviceType, mobileModel, osName, osVersion } from 'react-device-detect';
import db from 'libs/db';

export default function VerifyOTP2(props) {

	const [otp, setOtp] = React.useState('');
	const [invalidOTP, setinvalidOTP] = React.useState(false);
	const [loading, setloading] = React.useState(true);
	const [mainOTP, setmainOTP] = React.useState('');
	const { state } = props.location
	// const state = {verify:"EMAIL",name:'Omkar',email:"obhogle@gmail.com",mobile:"9768928535",otp:"123456"}
	// const state = {verify:"MOBILE",name:'Omkar',email:"obhogle@gmail.com",mobile:"9768928535",otp:"123456"}

	React.useEffect(() => {
		if (state === null || state === undefined) {
			props.history.push('/signup')
		} else {
			sendOTP();
			setloading(false)
		}
	}, [])

	const validateInput = () => {
		return otp.length === 6
	}

	const onNextBtnPressed = () => {
		if (otp === `${mainOTP}` && mainOTP !== '') {
			setinvalidOTP(false);
			createUser(state.mobile)
		} else {
			setinvalidOTP(true);
		}
	}

	const sendOTP = async () => {
		try {
			setmainOTP('')
			const res = await AdminServices.generateOTP(state.verify === 'EMAIL' ? state.email : state.mobile, state.verify).then((res) => res)
			if (res.type) {
				if (res.data.Result === 'success') {
					setmainOTP(res.data.otpcode);
				} else {
					toast.error("Try again ...");
				}
			} else {
				toast.error("Try again ...");
			}
		} catch (error) {
			toast.error("Try again ...");
		}
	};

	const createUser = async (mobile = '') => {
		const data = {
			mobileNumber: mobile, // will be blank if skip is pressed
			email: state.email,
			clientName: state.name,
			source: deviceType + ":" + browserName + ":" + (mobileModel === "none" ? osName : mobileModel) + ":" + osVersion
		}
		console.log(data)
		try {
			const res = await AdminServices.createUser(data).then((res) => res)
			if (res.type) {
				if (res.data.Result === 'success') {
					console.log("user created")
					const userData = {
						name: state.name,
						email: state.email,
						mobile: mobile,
						id: res.data.ClientId,
						isLogged: true,
						profileComplete: false,
					};
					db.set(db.model.user, JSON.stringify({ userData }), true);
					props.history.push({
						pathname: '/dashboard',
						state: { ...state }
					})
				} else {
					toast.error("Try again ...");
				}
			} else {
				toast.error("Try again ...");
			}
		} catch (error) {
			toast.error("Try again ...");
		}
	}

	if (loading) {
		return (
			<div className="center-screen">
				<Spinner
					as="span"
					animation="grow"
					size="sm"
					role="status"
					aria-hidden="true"
				/>
			</div>
		);
	} else {
		return <div className="copycontainer">
			{
				state.verify === 'EMAIL' ?
					<AuthHeader navigation={props.history} left='login' />
					:
					<AuthHeader navigation={props.history} left='login' right="skip" onSkip={createUser} />
			}
			<div className="container">
				<h3 className="textHeadauth">Almost there!</h3>
				<p className="textNormal">
					Please enter the OTP received on <span className='underlineBlue'>
						{state.verify === 'EMAIL' ? state.email : state.mobile}</span>
				</p>
				<div>
					<FloatingTextInput
						className="letter-space"
						type="pin"
						id="typepass"
						label="OTP"
						autoFocus
						onChange={(z) => { setOtp(z.target.value) }}
						value={otp}
						maxLength={6}
					/>

					<CountDown totalTime={30000} dir="down" onClick={() => { sendOTP(); }} />

					{invalidOTP ? (
						<p className="textNormal" style={{ color: "red" }}>
							Incorrect OTP. Try again!
						</p>
					) : (true)}
				</div>
				<div className="footer">
					<Button style={{ opacity: validateInput() ? 1 : 0.4 }}
						onClick={() => { validateInput() && onNextBtnPressed() }}
						variant="primary" className="btn-footer">
						Verify <AiOutlineRight size={14} />
					</Button>
				</div>
			</div>
		</div>
	}
}
