import React from 'react';
import { AiOutlineLeft, AiOutlineRight } from 'react-icons/ai';
import { FiArrowLeft } from "react-icons/fi";
import cssVar from 'views/mobileView/styles';
import mintbox from "../../../../../assets/SVG/mintbox_color_logo.svg";
const AuthHeader = ({
  navigation,
  right = '',
  left='',
  onSkip = () =>{}
}) => {

  const renderRightSide = () => {
    switch (right) {
      case 'logout':
        return (<span onClick={() => {
          localStorage.clear();
          navigation.push("/signup")
        }}
          style={{ color: cssVar.getPropertyValue('--primary') }}>Logout</span>);
      case 'skip':
        return (<span onClick={() => {
          onSkip();
        }}
          style={{ color: cssVar.getPropertyValue('--color-text') }}>Skip <AiOutlineRight size={10}/></span>
          );
      default:
        return <span></span>;
    }
  };

  const renderLeftSide = () => {
    switch (left) {
      case 'login':
        return (<span onClick={() => { navigation.goBack() }}
          style={{ color: cssVar.getPropertyValue('--color-text') }}><AiOutlineLeft size={10}/> Login</span>);
      case 'signup':
        return (<span onClick={() => { navigation.goBack() }}
          style={{ color: cssVar.getPropertyValue('--color-text') }}><AiOutlineLeft size={10}/> Sign up</span>);
      default:
        return <span></span>;
    }
  };

  return (
    <div className="container d-flex flex-row justify-content-between py-3" style={{backgroundColor:"#fafafa"}}>
      <div style={{ flex: 1 }} >
        {renderLeftSide()}
      </div>
      <div style={{ flex: 2 }} className="d-flex justify-content-center align-items-center">
        <img alt="" src={mintbox} height="23px" width="100%" />
      </div>
      <div style={{ flex: 1 ,textAlign:"right"}} >
        {renderRightSide()}
      </div>
    </div>
  );
};

export default AuthHeader;
