import React, { useState } from 'react';
import AdminServices from 'services/api/AdminServices';
import cssVar from 'views/mobileView/styles';

function MintboxConstituents(props) {

  const [mintboxConstituents, setMintboxConstituents] = useState([]);

  React.useEffect(() => {
    getMintboxConstituentsById(props.mintboxId);
  }, [])

  const getMintboxConstituentsById = async (id) => {
    let res = await AdminServices.getMintboxConstituentsById(id).then((res) => { return res })
    console.log("Get mintbox constituents API", res.data)
    setMintboxConstituents(res.data)
  }

  const displayAssetClass = (id) => {
    const assetClasses = [
      {
        name: 'Indian Equity',
        color: '#F97B30'
      },
      {
        name: 'Gold',
        color: '#F9CD22'
      },
      {
        name: 'Foreign Equity',
        color: '#E4332D'
      },
      {
        name: 'Debt',
        color: '#7453A0'
      },
      {
        name: 'Cash',
        color: '#52D29F'
      }
    ];
    const fundsArray = mintboxConstituents.filter(fund => fund.AssetClassId === id)
    const totalAllocation = fundsArray.reduce((total, fund) => fund.CurrentWeightage + total, 0);
    const assetClassName = assetClasses[id - 1].name
    const assetColor = assetClasses[id - 1].color

    console.log("Asset class ", id, "total : ", totalAllocation)
    if (fundsArray.length === 0) { return }
    else return (
      <>
        <div style={{ display: "flex", justifyContent: "space-between", color: assetColor, borderBottom: "1px solid #e5e5e5", padding: "2% 0", fontSize: "14px", fontWeight: "500" }}>
          <div>{assetClassName}</div>
          <div>{totalAllocation}</div>
        </div>
        {
          fundsArray.map(fund => {
            return <div key={fund.FundCode} style={{ display: "flex", justifyContent: "space-between", borderLeft: "2px solid " + assetColor, borderBottom: "1px solid #e5e5e5", padding: "2% 0 2% 2%", fontSize: "14px", fontWeight: "400" }}>
              <div style={{ width: "60%" }}>{fund.ETFName}</div>
              <div style={{ margin: "auto 0" }}>{fund.CurrentWeightage}</div>
            </div>
          })
        }
      </>
    )
  }
  return (
    <div style={{ padding: "4%", borderBottom: "1px solid #e5e5e5" }}>
      <p>This is a fixed asset allocation mintbox. It will be rebalanced every quarter to reflect the following target allocation.</p>
      <div style={{ color: cssVar.getPropertyValue('--primary'), fontSize: "14px", fontWeight: "500" }}>Understand how rebalancing works &gt;</div>
      <div style={{ margin: "4% 0", display: "flex", justifyContent: "space-between" }}>
        <div style={{ color: "#6C6C6C", fontWeight: "500", fontSize: "14px" }}>Asset class/ETF</div>
        <button style={{ color: "#294C88", fontSize: "12px", border: "1px solid #294C88", borderRadius: '57px', height: "24px", background: "transparent", padding: "0 8px" }}>&lt; Target allocation (%) &gt;</button>
      </div>
      {displayAssetClass(1)}
      {displayAssetClass(2)}
      {displayAssetClass(3)}
      {displayAssetClass(4)}
      {displayAssetClass(5)}
      <div style={{ display: "flex", justifyContent: "space-between", color: "#6c6c6c", padding: "2% 0", fontSize: "14px", fontWeight: "500" }}>
        <div>Total</div>
        <div>100</div>
      </div>
    </div >
  )
}

export default MintboxConstituents