import React from 'react';
import { Button, Modal, Spinner } from 'react-bootstrap';
import AdminServices from 'services/api/AdminServices';
import colorsFinal from 'theme/colors';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import ProfileCompleteHeader from 'views/mobileView/components/molecules/Headers/ProfileCompletionHeader';
import cssVar from 'views/mobileView/styles';
import db from 'libs/db';
import { dropdownCustom } from 'views/mobileView/components/atoms/DataCustomizer';
import CustomDropDown from 'views/mobileView/components/molecules/DropDown/CustomDropDown';
import BottomBackNextBtn from 'views/mobileView/components/atoms/BottomBackNextBtn';

const FAddress = (props) => {

    const [countryList, setcountryList] = React.useState([]);

    const [line1, setLine1] = React.useState('');
    const [line2, setLine2] = React.useState('');
    const [zipCode, setzipCode] = React.useState('');
    const [city, setCity] = React.useState('');
    const [country, setCountry] = React.useState({});
    const [selectedItem, setselectedItem] = React.useState([]);

    const [loading, setloading] = React.useState(true);
    const [nextdisabled, setnextDisabled] = React.useState(true);
    const [value, setValue] = React.useState('');

    const [helperType, sethelperType] = React.useState('');
    const [helperColor, sethelperColor] = React.useState('');
    const [helperText, sethelperText] = React.useState('');
    const dataAdult = {
        head: 'Address'
    };

    const dataMinor = {
        head: 'Postal Address'
    };

    const dataNri = {
        head: 'Foreign Address'
    };

    const checkOnBlur = (data) => {
        if (data.length !== 6 || isNaN(data)) {
            sethelperType('pin');
            sethelperColor('red');
            sethelperText('Enter a valid Zip code');
        } else {
            sethelperType('');
            sethelperColor('');
            sethelperText('');
        }
    };

    const checkValidation = () => {
        if (
            line1.length !== 0
            && line2.length > 1
            && zipCode.length > 1
            && city.length !== 0
            && Object.entries(country).length !== 0
        ) {
            setnextDisabled(false);
        } else {
            setnextDisabled(true);
        }
    };

    const getData = async () => {
        const res = await AdminServices.getCountries().then((res) => res);
        // console.log('DATA : ', res);
        const data = await dropdownCustom(res.data, ['UID', 'Country']);
        // console.log('sd', data);
        setcountryList(data);
    };

    React.useEffect(() => {
        checkValidation();
    });

    React.useEffect(() => {
        db.get(db.model.userData).then((u) => {
            if (u) {
                // u = JSON.parse(u);
                console.log(u)
                if (u.forAdd) {
                    setLine1(u.forAdd.line1 || '');
                    setLine2(u.forAdd.line2 || '');
                    setzipCode(u.forAdd.zipCode || '');
                    setCity(u.forAdd.city || '');
                    setCountry(u.forAdd.country || '');
                    setcountryList(u.forAdd.allcountry || []);
                    setloading(false);
                } else {
                    getData().then(() => {
                        setloading(false);
                    });
                }
            }
        });

        checkValidation();
    }, []);

    if (loading) {
        return (
            <div className="center-screen">
                <Spinner
                    as="span"
                    animation="grow"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                />
            </div>
        );
    } else {
        return (
            <div className="container">
                {/* <ProfileCompleteHeader
                    header={dataNri.head}
                    navigation={props.history}
                    percentage={30} /> */}
                <div className="row">
                    <div className="col-6">
                        <div className="mt-4 mb-2">
                            <div className="floating-form">
                                <FloatingTextInput
                                    type="text"
                                    id="line1"
                                    label="Line 1"
                                    onChange={(val) => {
                                        setLine1(val.target.value)
                                    }}
                                    onBlur={() => {
                                        if (line1.length === 0) {
                                            sethelperType('line1');
                                            sethelperColor('red');
                                            sethelperText('Line 1 can\'t be empty');
                                        } else {
                                            sethelperType('');
                                            sethelperColor('');
                                            sethelperText('');
                                        }
                                    }}
                                    autoCapitalize="words"
                                    value={line1}
                                />
                                {helperType === 'line1' ? (
                                    <span style={{ fontSize: cssVar.getPropertyValue('--font-helper'), color: helperColor }}>
                                        {helperText}
                                    </span>
                                ) : null}

                                <FloatingTextInput
                                    type="text"
                                    id="line2"
                                    label="Line 2"
                                    autoCapitalize="words"
                                    value={line2}
                                    onChange={(val) => {
                                        setLine2(val.target.value)
                                        console.log(val.target.value)
                                    }}
                                />

                                <FloatingTextInput
                                    type="tel"
                                    id="zip"
                                    label="ZIP code"
                                    maxLength="6"
                                    // error={helperType === 'pin'}
                                    value={zipCode}
                                    onBlur={() => checkOnBlur(zipCode)}
                                    onChange={(val) => {
                                        setzipCode(val.target.value)
                                        console.log(val.target.value)
                                    }}
                                />
                                {helperType === 'pin' ? (
                                    <span style={{ fontSize: cssVar.getPropertyValue('--font-helper'), color: helperColor }}>
                                        {helperText}
                                    </span>
                                ) : null}

                                <FloatingTextInput
                                    type="text"
                                    id="city"
                                    label="City"
                                    onChange={(val) => {
                                        setCity(val.target.value)
                                        console.log(val.target.value)
                                    }}
                                    onBlur={() => {
                                        if (city.length === 0) {
                                            sethelperType('city');
                                            sethelperColor('red');
                                            sethelperText('city can\'t be empty');
                                        } else {
                                            sethelperType('');
                                            sethelperColor('');
                                            sethelperText('');
                                        }
                                    }}
                                    autoCapitalize="words"
                                    value={city}
                                />
                                {helperType === 'city' ? (
                                    <span style={{ fontSize: cssVar.getPropertyValue('--font-helper'), color: helperColor }}>
                                        {helperText}
                                    </span>
                                ) : null}

                                <CustomDropDown
                                    placeholder="Enter country name"
                                    label="Country"
                                    initial={country}
                                    items={countryList}
                                    onChange={(item) => setCountry(item)} />
                            </div>
                        </div>
                        <div>
                            <BottomBackNextBtn
                                navigation={props.history}
                                next="/admin/client/complete-pro/fatca"
                                params={props.location.state}
                                saveData={{
                                    forAdd: {
                                        line1,
                                        line2,
                                        zipCode,
                                        city,
                                        country,
                                        allcountry: countryList
                                    }
                                }}
                                nextdisabled={nextdisabled} />
                        </div>
                    </div>
                    <div className="col-6"></div>
                </div>
            </div>
        );
    }
};

export default FAddress;
