import db from 'libs/db';
import React from 'react';
import { Button, Spinner } from 'react-bootstrap';
import { AiOutlineRight } from 'react-icons/ai';
import { toast } from 'react-toastify';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import AdminServices from '../../../../../services/api/AdminServices';
import CountDown from '../../../components/atoms/CountDown';
import AuthHeader from '../../../components/molecules/Headers/AuthHeader';

export default function LoginOTPScreen(props) {

	const [otp, setOtp] = React.useState('');
	const [invalidOTP, setinvalidOTP] = React.useState(false);
	const [loading, setloading] = React.useState(true);
	const [mainOTP, setmainOTP] = React.useState('');
	const { state } = props.location
	// const state = {ClientName:"Omkar",otp:"123456",Email:"obhogle@gmail.com",MobileNumber:"9768928535",ClientID:1}

	React.useEffect(() => {
		if (state === null || state === undefined) {
			props.history.push('/signup')
		} else {
			sendOTP();
			setloading(false);
			console.log(state)
		}
	}, [])
	const validateInput = () => {
		return otp.length === 6
	}

	const onNextBtnPressed = () => {
		if (otp === `${mainOTP}` && mainOTP !== '') {
			setinvalidOTP(false);
			const userData = {
				name: state.ClientName,
				email: state.Email,
				mobile: state.MobileNumber,
				id: state.ClientId,
				isLogged: true,
				profileComplete: false,
			};
			db.set(db.model.user, JSON.stringify({ userData }), true);
			props.history.push({
				pathname: '/dashboard',
				state: { ...state }
			})
		} else {
			setinvalidOTP(true);
		}
	}

	const sendOTP = async () => {
		try {
			setmainOTP('')
			setloading(true);
			const res = await AdminServices.login(state.ClientId).then((res) => res);
			console.log(res)
			if (res.type) {
				if (res.data.Result === 'success') {
					setmainOTP(res.data.otpcode);
					setloading(false);
				} else {
					toast.error("Try again ...");
					setloading(false);
				}
			} else {
				toast.error("Try again ...");
				setloading(false);
			}
		} catch (error) {
			toast.error("Try again ...");
			setloading(false);
		}
	};

	if (loading) {
		return (
			<div className="center-screen">
				<Spinner
					as="span"
					animation="grow"
					size="sm"
					role="status"
					aria-hidden="true"
				/>
			</div>
		);
	} else {
		return (
			<div className="copycontainer">
				<AuthHeader navigation={props.history} left='signup' />
				<div className="container">
					<h3 className="textHeadauth">{"Welcome Back, " + state.ClientName + "!"}</h3>
					<p className="textNormal">Please enter OTP received on {state.Email} and {state.MobileNumber}</p>
					<div>
						<FloatingTextInput
							className="letter-space"
							type="pin"
							id="typepass"
							label="OTP"
							autoFocus
							onChange={(z) => {
								setOtp(z.target.value);
							}}
							value={otp}
							maxLength={6}
						/>

						<CountDown totalTime={30000} dir="down" onClick={() => { sendOTP(); }} />

						{invalidOTP ? (
							<p className="textNormal" style={{ color: "red" }}>
								Incorrect OTP. Try again!
							</p>
						) : (true)}
					</div>
					<div className="footer">
						<Button style={{ opacity: validateInput() ? 1 : 0.4 }}
							onClick={() => { validateInput() && onNextBtnPressed() }}
							variant="primary" className="btn-footer">
							Login <AiOutlineRight size={14} />
						</Button>
					</div>
				</div>
			</div>
		)
	}
}
