import React from 'react'
import { toast } from "react-toastify";
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader'
import { FiMinusCircle } from 'react-icons/fi'
import cssVar from 'views/mobileView/styles'
import db from 'libs/db'
import AdminServices from 'services/api/AdminServices';
import { MintboxSmallCube } from "assets/SVG";
import SearchBar from 'views/mobileView/components/atoms/SearchBar'
import { setCreateMintboxData } from 'redux/CreateMintbox/actions';
import { data } from 'jquery';

const BuildMintbox = (props) => {

    const [selectedETFs, setSelectedETFs] = React.useState([]);
    const [allETFs, setAllETFs] = React.useState([]);

    const [loading, setloading] = React.useState(false);

    React.useEffect(async () => {
        await getSelectedETFs();
        getAllETFs();
    }, []);

    React.useEffect(() => {
        saveData(selectedETFs)
    }, [selectedETFs]);

    const getSelectedETFs = async () => {
        try {
            await db.get(db.model.createMintbox).then((u) => {
                if (u?.selectedETFs) {
                    setSelectedETFs(u.selectedETFs)
                }
            })
        } catch (error) {
            toast.error(error);
        }
    };

    const saveData = async (arr) => {
        const dataFinal = {
            selectedETFs: arr
        };
        await db.get(db.model.createMintbox).then((u) => {
            if (u) { Object.assign(u, dataFinal) }
            else { u = dataFinal }
            setCreateMintboxData(u);
        })
    }

    const getAllETFs = async () => {
        try {
            console.log("start")
            await db.get(db.model.createMintbox).then(async (u) => {
                console.log("u",u)
                if (u?.allETFs) {
                    console.log("all etf")
                    setAllETFs(u.allETFs)
                } else {
                    let res = await AdminServices.getAllETFs().then((res) => JSON.stringify(res));
                    res = await JSON.parse(res);
                    if (res.type === true) {
                        console.log("Get All ETFs API: ", res.data)
                        let newArr = res.data.map(fund => { return { ...fund, weight: '0', isLocked: false } })
                        const dataFinal = {
                            'allETFs': newArr
                        };
                        console.log(dataFinal)
                        if (u) { Object.assign(u, dataFinal) }
                        else { u = dataFinal }
                        setCreateMintboxData(u);
                        setAllETFs(newArr)
                    }
                }
            })
        } catch (error) {
            toast.error(error);
        }
    }


    const onPlusIconClick = async (uid) => {
        const newETF = await allETFs.find((fund) => fund.UID === uid);
        setSelectedETFs([...selectedETFs, newETF])
    }

    const Footer = () => {
        const initializeEqualWeights = async (arr) => {
            const unlockedETFsCount = arr.filter(f=>!f.isLocked).length;
            if(unlockedETFsCount === 0 ){
                return
            }
            const lockedWeight = arr.reduce((sum,f)=>{
                if(f.isLocked){
                    return sum + f.weight
                }
                return sum
            },0)
            console.log("lockedWeight",lockedWeight)
            const remainingWeight = 100 - lockedWeight;
            const firstNotLockedIndex = arr.findIndex((f)=>!f.isLocked)
            console.log('unlockedETFsCount:',unlockedETFsCount)
            console.log('remainingWeight:',remainingWeight)
            let newArr = arr.map((fund,i)=>{
                if(fund.isLocked){
                    return fund;
                }
                if (i === firstNotLockedIndex) { return { ...fund, weight: Math.floor(remainingWeight / unlockedETFsCount) + remainingWeight % unlockedETFsCount } }
                else return { ...fund, weight: Math.floor(remainingWeight / unlockedETFsCount) }
            })
            saveData(newArr)
        }
        
        const onNextPress = async () => {
            if (selectedETFs.length >= 2) {
                await initializeEqualWeights(selectedETFs);
                props.history.push('/build-your-own-mintbox/define-weights')
            }
        }

        return (
            <>
                <div style={{ position: "fixed", bottom: 0, width: "100%" }}>
                    <div style={{ fontSize: "14px", fontWeight: '400', color: "6c6c6c", textAlign: "center", borderTop: "1px solid #e5e5e5", padding: "2% 4%", background: "#FFFFFF" }}>
                        Minimum Investment <b>RS 12344</b>
                    </div>
                    <div style={{ background: "#FAFAFA", display: "flex", borderTop: "1px solid #e5e5e5" }}>
                        <div style={{ flex: 1, padding: "4%" }}>
                            <div style={{ fontSize: "14px", fontWeight: "400", marginBottom: "2%" }}>Define ETF weights in the next step</div>
                        </div>
                        <div style={{ flex: 1, padding: "4%" }}>
                            <button
                                style={{
                                    borderRadius: 4,
                                    fontSize: "14px",
                                    backgroundColor: cssVar.getPropertyValue("--brandSecondary"),
                                    color: "#FFFFFF",
                                    height: "44px",
                                    width: "100%",
                                    border: "none",
                                    opacity: selectedETFs.length >= 2?1:0.4,
                                }}
                                onClick={() => { onNextPress() }}
                            >
                                Confirm Selection
                            </button>
                        </div>
                    </div>
                </div>
            </>
        )
    }

    const DisplaySelectedETFs = () => {
        const onDelete = (id) => {
            setSelectedETFs(selectedETFs.filter(f => f.UID !== id));
        }
        return (
            <>
                <div style={{ margin: "4%", fontSize: "14px", fontWeight: "400", color: "#333333" }}><b>{selectedETFs.length}</b> ETFs selected</div>
                {selectedETFs.map((fund) => {
                    return (
                        <div style={{ padding: "4%", borderBottom: "1px solid #e5e5e5" }} key={fund.UID}>
                            <div style={{ display: "flex" }}>
                                <img src={MintboxSmallCube} style={{ width: "20px", height: "20px", padding: "2px", marginRight: "4%", border: "1px solid #e5e5e5", borderRadius: 5 }} />
                                <div style={{ flex: "1" }}>
                                    <div style={{ color: "#333333", fontSize: "14px", fontWeight: "500" }}>{fund.ETFName}</div>
                                    <div style={{ color: "#6C6C6C", fontSize: "12px", fontWeight: "400" }}>{"NAV  " + fund.NAV}</div>
                                </div>
                                <FiMinusCircle size={26} style={{ background: "#EA2F2F1F", color: '#EA2F2F', borderRadius: "50%", margin: "auto 0 auto 2%" }} onClick={() => onDelete(fund.UID)} />
                            </div>
                        </div>
                    )
                })}
                {selectedETFs.length === 1 && <div style={{ margin: "4%", fontSize: "14px", fontWeight: "400", color: "#333333" }}>Add at least <b>one more</b> ETF to build your own mintbox</div>}
            </>
        )
    }

    return (
        <div>
            <MainHeader
                header="Build your own mintbox"
                right="help"
                screenType="Invest"
                navigation={props.history}
            />
            <div className="containerData px-0 pb-4" style={{ marginBottom: "120px" }}>
                <SearchBar type="ETF" allFunds={allETFs.filter(f => !selectedETFs.some(x => x.UID === f.UID))} showPlusIcon={true} onPlusIconClick={onPlusIconClick} />
                {selectedETFs.length === 0 ?
                    <div style={{ margin: "4%" }}>
                        <div style={{ width: "100%", height: "165px", background: "rgb(244, 248, 255)", border: "1px solid rgb(229, 229, 229)", borderRadius: "6px" }}></div>
                        <p style={{ marginTop: "4%" }}>Add ETFs to start building your own mintbox</p>
                    </div>
                    :
                    <>
                        <DisplaySelectedETFs />
                        <Footer />
                    </>
                }
            </div>
        </div>
    )
}

export default BuildMintbox
