import React from 'react'
import AdminServices from 'services/api/AdminServices';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { ManWithCalculator, MintboxSmallCube } from "assets/SVG";
import { BsBookmark, BsBookmarkFill } from 'react-icons/bs';
import cssVar from 'views/mobileView/styles';
import { FiSearch } from 'react-icons/fi'

export default function ListETFs(props) {

  const [allETFs, setAllETFs] = React.useState([]);
  React.useEffect(() => {
    loadData();
  }, []);

  const loadData = async () => {
    getAllETFs();
  }

  const getAllETFs = async () => {
    let res = await AdminServices.getAllETFs().then((res) => JSON.stringify(res));
    res = await JSON.parse(res);
    console.log("Get All ETFs API: ", res.data)
    if (res.type === true) {
      setAllETFs(res.data)
      console.log(allETFs)
    }
  }
  const FundCard = ({ fund }) => {
    return (
      <div style={{ padding: "4%", borderBottom: "1px solid #e5e5e5" }}>
        <div style={{ display: "flex" }}>
          <div style={{ width: "24px", height: "24px", padding: "4px", marginRight: "4%", backgroundColor: "#F2FBFB", borderRadius: 5 }}>
            <img src={MintboxSmallCube} style={{ width: "100%", verticalAlign: "unset" }} />
          </div>
          <div style={{ flex: "1" }}>
            <div style={{ color: "#333333", fontSize: "16px", fontWeight: "500", marginRight: "4%" }}>{fund.ETFName}</div>
            <div style={{ fontSize: cssVar.getPropertyValue('--font-helper'), fontWeight: "400", color: cssVar.getPropertyValue("--textLight") }}>{fund.AssetClassId} Indian Equity ETF - Largecap</div>
          </div>
          <div style={{ margin: "1%", color: cssVar.getPropertyValue("--primary") }}>
            {fund.NAV}
          </div>
        </div>
      </div>
    )
  }
  return (
    <div>
      <MainHeader
        header="Invest in mintboxes"
        right="help"
        screenType="Invest"
        navigation={props.history}
      />
      <div className="containerData px-0 pb-4 mb-4">
        <div style={{ border: "1px solid " + cssVar.getPropertyValue("--divider"), margin: "4%", borderRadius: 4, padding: 4, display: "flex", flexDirection: "row", alignItems: "center" }}
          onClick={() => { }}>
          <FiSearch size={15} style={{ margin: "0 4%" }} />
          <div style={{ fontSize: 15, color: cssVar.getPropertyValue("--textLight"), marginTop: 2 }}>Search by ETF name</div>
        </div>
        {
          allETFs.map((fund, i) => {
            return (
              <FundCard fund={fund} key={i} />
            )
          })
        }
      </div>
    </div>
  )
}
