import React from 'react'
import AdminServices from 'services/api/AdminServices';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { ManWithCalculator, MintboxSmallCube } from "assets/SVG";
import { BsBookmark, BsBookmarkFill } from 'react-icons/bs';
import cssVar from 'views/mobileView/styles';
import { FiSearch } from 'react-icons/fi'
import { toast } from 'react-toastify';
import db from 'libs/db';
import { setCreateMintboxData } from 'redux/CreateMintbox/actions';

export default function ListMFs(props) {

  const [allMFs, setAllMFs] = React.useState([]);
  const [loading, setLoading] = React.useState(false);

  React.useEffect(() => {
    loadData();
  }, []);

  const loadData = async () => {
    getAllMFs();
  }

  const getAllMFs = async () => {
    setLoading(true);
    try {
      await db.get(db.model.createMintbox).then(async (u) => {
          if (u?.allMFs) {
              setAllMFs(u.allMFs);
              setLoading(false);
          } else {
              let res = await AdminServices.getFundsData().then((res) => JSON.stringify(res));
              res = await JSON.parse(res);
              if (res.type === true) {
                  console.log("Get All MFs API: ", res.data)
                  const dataFinal = {
                    allMFs : res.data
                  }
                  if (u) { Object.assign(u, dataFinal) }
                  else { u = dataFinal }
                  setCreateMintboxData(u);
                  setAllMFs(res.data)
                  setLoading(false);
              }
          }
      })
  } catch (error) {
    setLoading(false);
    toast.error(error);
  }
  }
  const FundCard = ({ fund }) => {
    return (
      <div style={{ padding: "4%", borderBottom: "1px solid #e5e5e5" }}>
        <div style={{ display: "flex" }}>
          <div style={{ width: "24px", height: "24px", padding: "4px", marginRight: "4%", backgroundColor: "#F2FBFB", borderRadius: 5 }}>
            <img src={MintboxSmallCube} style={{ width: "100%", verticalAlign: "unset" }} />
          </div>
          <div style={{ flex: "1" }}>
            <div style={{ color: "#333333", fontSize: "16px", fontWeight: "500", marginRight: "4%" }}>{fund.FundName}</div>
            <div style={{ fontSize: cssVar.getPropertyValue('--font-helper'), fontWeight: "400", color: cssVar.getPropertyValue("--textLight") }}>{fund.AssetClassId}</div>
          </div>
          <div style={{ margin: "1%", color: cssVar.getPropertyValue("--primary") }}>
            {fund.NAV}
          </div>
        </div>
      </div>
    )
  }
  return (
    <div>
      <MainHeader
        header="Invest in direct mutual funds"
        right="help"
        screenType="Invest"
        navigation={props.history}
      />
      <div className="containerData px-0 pb-4 mb-4">
        <div style={{ border: "1px solid " + cssVar.getPropertyValue("--divider"), margin: "4%", borderRadius: 4, padding: 4, display: "flex", flexDirection: "row", alignItems: "center" }}
          onClick={() => { }}>
          <FiSearch size={15} style={{ margin: "0 4%" }} />
          <div style={{ fontSize: 15, color: cssVar.getPropertyValue("--textLight"), marginTop: 2 }}>Search by mutual fund name</div>
        </div>
        {
          allMFs.map((fund, i) => {
            return (
              <FundCard fund={fund} key={i} />
            )
          })
        }
      </div>
    </div>
  )
}
