import React from 'react';
import { Button, Spinner } from 'react-bootstrap';
import { toast } from 'react-toastify';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import { emailValidator } from 'libs/validators';
import AdminServices from 'services/api/AdminServices';
import AuthHeader from 'views/mobileView/components/molecules/Headers/AuthHeader';
import db from 'libs/db';
import { setUserData } from 'redux/CompleteProfile/actions';

export default function SignupScreen(props) {

    const [name, setName] = React.useState('');
    const [email, setEmail] = React.useState('');
    const [phone, setPhone] = React.useState('');
    const [nextbtnPressed, setnextbtnPressed] = React.useState(false);
    const [loading, setloading] = React.useState(true);
    const [helperType, sethelperType] = React.useState('');
    const [helperColor, sethelperColor] = React.useState('');
    const [helperText, sethelperText] = React.useState('');

    const onName = (n) => {
        setName(n);
        if (n.match(/[^a-zA-Z\s]/g)) {
            sethelperType('name');
            sethelperColor('red');
            sethelperText("Name can't be alphanumeric");
        } else {
            sethelperType('');
            sethelperColor('');
            sethelperText('');
        }
    };

    const onPhone = (p) => {
        console.log(p.length)
        setPhone(p);
        setnextbtnPressed(false);

        if (p.match(/[^0-9\s]/g)) {
            sethelperType('phone');
            sethelperColor('red');
            sethelperText('Enter valid number');
        } else {
            sethelperType('');
            sethelperColor('');
            sethelperText('');
        }
    };


    const validInput = () => name
        && emailValidator(email).isValid
        && phone.length === 10
        && helperText === ''

    const onNextBtnPressed = async () => {
        setnextbtnPressed(true);
        const dataFinal = {
            clientDetails: {
                name,
                phone,
                email
            }
        };

        db.get(db.model.userData).then((u) => {
            if (u) {
                Object.assign(u, dataFinal);
            } else {
                u = dataFinal;
            }
            setUserData(u).then(() => {
                props.history.push({
                    pathname: '/admin/client/complete-pro/pan',
                })
            })
        });
    }
    React.useEffect(() => {
        setloading(false)
        setnextbtnPressed(false);
    }, [])


    if (loading) {
        return (
            <div className="center-screen">
                <Spinner
                    as="span"
                    animation="grow"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                />
            </div>
        );
    } else {
        return <div className="container">
            <div className="row">
                <div className="col-6">
                    <div className="floating-form">
                        <FloatingTextInput
                            label="First Name"
                            onChange={(p) => onName(p.target.value)}
                            value={name}
                        />
                        {helperType === "name" ? <span className="textHelper primary" style={{ color: helperColor }} >{helperText}</span> : null}

                        <FloatingTextInput
                            label="Phone Number"
                            type="tel"
                            maxLength={10}
                            onChange={(p) => onPhone(p.target.value)}
                            value={phone}
                        />
                        {helperType === "phone" ? <span className="textHelper primary" style={{ color: helperColor }} >{helperText}</span> : null}
                        <FloatingTextInput
                            label="Email"
                            type="email"
                            editable={false}
                            onChange={(p) => setEmail(p.target.value)}
                            value={email}
                        />
                    </div>
                    <div>
                        {nextbtnPressed ? (
                            <>
                                <Button variant="outline-light" style={{
                                    color: '#fff',
                                    backgroundColor: '#00A6A4',
                                    opacity: 0.4,
                                    borderRadius: 10,
                                    width: '100%',
                                    height: '44px'
                                }}>
                                    <Spinner
                                        as="span"
                                        animation="grow"
                                        size="sm"
                                        role="status"
                                        aria-hidden="true"
                                    />
                                    Next &#8594;
                                </Button>

                            </>
                        ) : (
                            <Button variant="outline-light" style={{
                                color: '#fff',
                                backgroundColor: '#00A6A4',
                                borderRadius: 10,
                                width: '100%',
                                height: '44px',
                                opacity: validInput() ? 1 : 0.4,
                            }}
                                onClick={() => onNextBtnPressed()}
                            >
                                Next &#8594;
                            </Button>)}
                    </div>
                </div>
                <div className="col-6">Info</div>
            </div>
        </div>
    }
}
