import React from 'react'
import { BsChevronDown, BsChevronUp } from 'react-icons/bs';
import cssVar from "views/mobileView/styles";

export default function ReadMoreText({ text }) {

  const [isReadMore, setIsReadMore] = React.useState(true);

  if (text.length < 100) {
    return text
  }
  else return <>
    {isReadMore ? text.slice(0, 80) + "..." : text}
    <span onClick={() => setIsReadMore(!isReadMore)} style={{ color: cssVar.getPropertyValue('--brandSecondary'), float: "right", lineHeight: "24px" }}>
      {isReadMore ? " Read More " : " Show Less "}{isReadMore ? <BsChevronDown size={12} /> : <BsChevronUp size={12} />}
    </span>
  </>
}