import React from 'react'
import { MintboxSmallCube } from "assets/SVG";
import {  BsPlusCircle } from 'react-icons/bs';
import cssVar from 'views/mobileView/styles';
import { FiSearch } from 'react-icons/fi'
export default function SearchBar({ type="Mintbox", allFunds, onSearchResultClick=()=>{}, showPlusIcon=false, onPlusIconClick=()=>{} }) {

    const [searchInput, setSearchInput] = React.useState('');
    
    const SearchResultCard = ({ fund }) => {
        return (
            <div style={{ margin: "0 4%", padding: "4%", border: "1px solid #e5e5e5", borderTop: "none" }} onClick={() => {onSearchResultClick(fund[type+'Id']) }}>
                <div style={{ display: "flex" }}>
                    <img src={MintboxSmallCube} style={{ width: "20px", height: "20px", padding: "2px", marginRight: "4%", backgroundColor: "#F2FBFB", borderRadius: 5 }} />
                    <div style={{ flex: "1" }}>
                        <div style={{ color: "#333333", fontSize: "14px", fontWeight: "500" }}>{fund[type+'Name']}</div>
                       {type==='ETF' && <div style={{ color: "#6C6C6C", fontSize: "12px", fontWeight: "400" }}>{"NAV  " + fund.NAV}</div>}
                    </div>
                    {showPlusIcon && <BsPlusCircle size={26} style={{ background: "#F1FBF4", color: '#00A6A4', borderRadius: "50%", margin: "auto 0 auto 2%" }}
                     onClick={() => {
                         setSearchInput('')
                         onPlusIconClick(fund.UID) }} />}
                </div>
            </div>
        )
    }
    return (
        <div>
            <div style={{ border: "1px solid " + cssVar.getPropertyValue("--divider"), margin: "4% 4% 0 4%", borderRadius: 4, padding: 4, display: "flex", flexDirection: "row", alignItems: "center" }}
                onClick={() => { }}>
                <FiSearch size={15} style={{ margin: "0 4%" }} />
                <input autoFocus style={{ fontSize: 15, color: cssVar.getPropertyValue("--textLight"), border: "none", outline: "none", width: "100%" }} value={searchInput}
                    onChange={(e) => { setSearchInput(e.target.value)  }}
                />
            </div>
            {
                allFunds.map((fund, i) => {
                    if (searchInput !== '' && fund[type+'Name'].toLowerCase().includes(searchInput.toLowerCase())) {
                        return (
                            <SearchResultCard fund={fund} key={i} />
                        )
                    }
                })
            }
        </div>
    )
}
