import { MintboxWhiteLogo } from 'assets/SVG'
import { useEffect, useState } from 'react'
import { FiBriefcase } from 'react-icons/fi'
import { HiOutlineCurrencyRupee, HiOutlineHome, HiOutlineMenu } from 'react-icons/hi'
import { IoGiftOutline } from 'react-icons/io5'
import { Link, useHistory, useLocation } from 'react-router-dom'
import colorsFinal from 'theme/colors'

const BottomTabs = props => {
  const location = useLocation();
  const [activeTabs, setActiveTabs] = useState('invest-mintboxes')

  useEffect(() => {
    if (location.pathname.includes("invest-MFs")) {
      setActiveTabs('invest-MFs')
    } else if (location.pathname.includes("invest-mintboxes")) {
      setActiveTabs('invest-mintboxes')
    } else if (location.pathname.includes("invest-ETFs")) {
      setActiveTabs('invest-ETFs')
    }
  })

  return (
    <div className='bottom-nav'>
      <div className='bn-tab'>
        <Link style={{ textDecoration: 'none' }} to="/invest-MFs"
          onClick={() => setActiveTabs('invest-MFs')}>
          <FiBriefcase
            size='20'
            color={activeTabs !== 'invest-MFs' ? '#999999' : '#000000'}
          />
          <p className="textNav" style={{ marginTop: "5px", color: activeTabs !== 'invest-MFs' ? '#999999' : '#000000' }}
          >Direct mutual funds</p>
        </Link>
      </div>
      <div className='bn-tab'>
        <Link style={{ textDecoration: 'none' }} to="/invest-mintboxes"
          onClick={() => setActiveTabs('invest-mintboxes')}>
          <FiBriefcase
            size='20'
            color={activeTabs !== 'invest-mintboxes' ? '#999999' : '#000000'}
          />
          <p className="textNav" style={{ marginTop: "5px", color: activeTabs !== 'invest-mintboxes' ? '#999999' : '#000000' }}
          >Smart mintboxes</p>
        </Link>
      </div>
      <div className='bn-tab'>
        <Link style={{ textDecoration: 'none' }} to="/invest-ETFs"
          onClick={() => setActiveTabs('invest-ETFs')}>
          <FiBriefcase
            size='20'
            color={activeTabs !== 'invest-ETFs' ? '#999999' : '#000000'}
          />
          <p className="textNav" style={{ marginTop: "5px", color: activeTabs !== 'invest-ETFs' ? '#999999' : '#000000' }}
          >Low cost ETFs</p>
        </Link>
      </div>
    </div>
  )
}

export default BottomTabs