import AdminServices from "./api/AdminServices"
import jwt_decode from "jwt-decode";


var smallcaseToken = '';

const SmallcaseGateway = () => {
  const scGateway = new window.scDK({
        gateway: 'mintbox',
        smallcaseAuthToken: smallcaseToken,
        config: {
            // Should allow after market orders?
            amo: true
        }
    })
}

const getsmallCaseToken = async () => {
    var res = await AdminServices.getSmallcasetoken('').then((rs) => { return rs })
    console.log("New Token =>>", res.data)
    localStorage.setItem("smallcaseSDKToken", res.data)
    // setSmallcaseToken(res.data)
    smallcaseToken = res.data

    console.log('\n starting init');
    try {
        await SmallcaseGateway()
        console.log('\n init success');
    } catch (err) {
        console.log('\n error during init' + JSON.stringify(err.userInfo));
        getsmallCaseToken()
    }
}

const smallCaseFun = async () => {

    console.log('\n setting config');

    var sdkToken = await localStorage.getItem('smallcaseSDKToken')
    if (sdkToken === null || sdkToken === undefined) {
        // console.log("hey")
        getsmallCaseToken()
    } else {
        if (jwt_decode(sdkToken).exp * 1000 < new Date().getTime()) {
            // console.log("hey2")
            getsmallCaseToken()
        } else {
            console.log("Token =>.", sdkToken)
            // setSmallcaseToken(sdkToken)
            smallcaseToken = sdkToken
        }
    }
}



export { smallCaseFun, SmallcaseGateway };
