import React from "react";
import { Button, Spinner } from "react-bootstrap";
import { toast } from 'react-toastify';
import colorsFinal from "theme/colors";
import FloatingTextInput from "views/mobileView/components/atoms/FloatingTextInput";
import { emailValidator } from "../../../../../libs/validators";
import AdminServices from "../../../../../services/api/AdminServices";
import AuthHeader from "../../../components/molecules/Headers/AuthHeader";
import { AiOutlineRight } from 'react-icons/ai';
export default function SignupScreen(props) {
	const [email, setEmail] = React.useState('');
	const [mobile, setMobile] = React.useState('');
	const [loading, setloading] = React.useState(true);
	const [loginUsingEmail, setLoginUsingEmail] = React.useState(false)
	const [nextbtnPressed, setnextbtnPressed] = React.useState(false)

	React.useEffect(() => {
		var aa = localStorage.getItem("lastEmail")
		setEmail(aa !== null ? aa : '');
		var x = localStorage.getItem("lastMobile");
		setMobile(x !== null ? x : '')
		setloading(false);
		setnextbtnPressed(false)
	}, []);

	const validateInput = () => {
		if (loginUsingEmail) {
			return emailValidator(email).isValid && !nextbtnPressed
		}
		else { return mobile.length === 10 && !nextbtnPressed }
	}

	const onNextBtnPressed = async () => {
		setnextbtnPressed(true);
		if (loginUsingEmail) {
			try {
				const res = await AdminServices.clientExists(email, "EMAIL").then((res) => res);
				if (res.type) {
					if (res.data.ClientId !== '') {
						localStorage.setItem("lastEmail", res.data.Email)
						localStorage.setItem("lastMobile", res.data.MobileNumber)
						props.history.push({
							pathname: '/login_otp',
							state: { ...res.data }
						})
						setnextbtnPressed(false);
					} else {
						localStorage.setItem("lastEmail", email)
						props.history.push({
							pathname: '/first_name',
							state: { email, verify: "EMAIL" }
						})
						setnextbtnPressed(false);
					}
				} else {
					toast.error("Try again ...");
					setnextbtnPressed(false);
				}
			} catch (error) {
				toast.error("Try again ...");
				setnextbtnPressed(false);
			}
		}
		else {
			try {
				const res = await AdminServices.clientExists(mobile, "MOBILE").then((res) => res);
				if (res.type) {
					if (res.data.ClientId !== '') {
						localStorage.setItem("lastEmail", res.data?.Email)
						localStorage.setItem("lastMobile", res.data?.MobileNumber)
						props.history.push({
							pathname: '/login_otp',
							state: { ...res.data }
						})
						setnextbtnPressed(false);
					} else {
						localStorage.setItem("lastMobile", mobile)
						props.history.push({
							pathname: '/first_name',
							state: { mobile, verify: "MOBILE" }
						})
						setnextbtnPressed(false);
					}
				} else {
					toast.error("Try again ...");
					setnextbtnPressed(false);
				}
			} catch (error) {
				toast.error("Try again ...");
				setnextbtnPressed(false);
			}
		}
	};

	if (loading) {
		return (
			<div className="center-screen">
				<Spinner
					as="span"
					animation="grow"
					size="sm"
					role="status"
					aria-hidden="true"
				/>
			</div>
		);
	} else {
		return (
			<div className="copycontainer">
				<AuthHeader />
			<div className="container">
				<h3 className="textHeadauth">{loginUsingEmail ? "Login/Sign up using email" : "Login/Sign up using mobile number"}</h3>
				<p className="textNormal">{loginUsingEmail ? "Please enter your email address" : "Please enter your 10 digit indian mobile number"}</p>
				<div className="floating-form">
					{loginUsingEmail ?
						<FloatingTextInput
							type="email"
							label="Email address"
							autoFocus
							onChange={(p) => setEmail(p.target.value)}
							value={email}
						/> :
						<FloatingTextInput
							type="tel"
							label="Mobile number"
							autoFocus
							id="Mobile-number"
							placeholder=" "
							maxLength={10}
							value={mobile}
							onChange={(num) => {
								const newMobile = num.target.value.replace(/\D/g, '');
								setMobile(newMobile);
							}}
						/>
					}
				</div>
				<p className="textNormal" style={{ color: colorsFinal.brandSecondary, marginTop: "5%" }} onClick={() => { setLoginUsingEmail(!loginUsingEmail) }}>
					{loginUsingEmail ? "Use mobile number instead" : "Use email instead"} <AiOutlineRight size={10} />
				</p>
				<div className="footer">
					<Button style={{ opacity: validateInput() ? 1 : 0.4 }}
						onClick={() => { validateInput() && onNextBtnPressed() }}
						// onClick={()=>props.history.push({
						// 	pathname: '/first_name'
						// })}
						variant="primary" className="btn-footer">
						Next <AiOutlineRight size={14} />
					</Button>
				</div>
			</div>
			</div>
		);
	}
}
