import React from 'react'
import AdminServices from 'services/api/AdminServices';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { ManWithCalculator, MintboxSmallCube } from "assets/SVG";
import { BsBookmark, BsBookmarkFill } from 'react-icons/bs';
import cssVar from 'views/mobileView/styles';
import { FiSearch } from 'react-icons/fi'

export default function ListMintboxes(props) {

  const [allMintboxes, setAllMintboxes] = React.useState([]);
  React.useEffect(() => {
    loadData();
  }, []);

  const loadData = async () => {
    getAllMintbox();
  }

  const getAllMintbox = async () => {
    let res = await AdminServices.getAllMintboxes().then((res) => JSON.stringify(res));
    res = await JSON.parse(res);
    console.log("Get All Mintboxes API: ", res.data)
    if (res.type === true) {
      setAllMintboxes(res.data)
      console.log(allMintboxes)
    }
  }
  const MintboxCard = ({ mintbox }) => {
    return (
      <div style={{ padding: "4%", borderBottom: "1px solid #e5e5e5" }} onClick={() => { props.history.push("/mintbox-detail/" + mintbox.MintboxId) }}>
        <div style={{ display: "flex" }}>
          <div style={{ width: "48px", height: "48px", padding: "8px", marginRight: "4%", backgroundColor: "#F2FBFB", borderRadius: 5 }}>
            <img src={MintboxSmallCube} style={{ width: "100%" }} />
          </div>
          <div style={{ flex: "1" }}>
            <div style={{ color: "#333333", fontSize: "16px", fontWeight: "500" }}>{mintbox.MintboxName}</div>
            <p style={{ fontSize: cssVar.getPropertyValue('--font-helper'), fontWeight: "400", color: cssVar.getPropertyValue("--textLight") }}>{mintbox.RiskLevel} risk mintbox</p>

          </div>
          <div style={{ margin: "1%" }}>
            <BsBookmark size={24} />
          </div>
        </div>
        <div>
          <p style={{ color: "#6c6c6c", fontSize: "14px", fontWeight: "400" }}>{mintbox.About}</p>
        </div>
        <div style={{ display: "flex", marginRight: "4%" }}>
          <div style={{ flex: 1 }}>
            <div className="textCardHelper">CAGR</div>
            <div className="textNormal" style={{ color: cssVar.getPropertyValue("--primary") }}>{mintbox.XIRR}</div>
          </div>
          <div style={{ flex: 1 }}>
            <div className="textCardHelper">Ideal for</div>
            <div className="textNormal">&lt; 1 year</div>
          </div>
          <div style={{ flex: 1 }}>
            <div className="textCardHelper">Min. investment</div>
            <div className="textNormal">{mintbox.MinSIPAmount}</div>
          </div>
        </div>
      </div>
    )
  }
  return (
    <div>
      <MainHeader
        header="Invest in mintboxes"
        right="help"
        screenType="Invest"
        navigation={props.history}
      />
      <div className="containerData px-0 pb-4 mb-4">
        <div style={{ border: "1px solid " + cssVar.getPropertyValue("--divider"), margin: "4%", borderRadius: 4, padding: 4, display: "flex", flexDirection: "row", alignItems: "center" }}
          onClick={() => { }}>
          <FiSearch size={15} style={{ margin: "0 4%" }} />
          <div style={{ fontSize: 15, color: cssVar.getPropertyValue("--textLight"), marginTop: 2 }}>Search by mintbox name</div>
        </div>
        {
          allMintboxes.map((mintbox, i) => {
            return (
              <MintboxCard mintbox={mintbox} key={i} />
            )
          })
        }
      </div>
    </div>
  )
}
