	const loadData = async () => {
		try {
			db.get(db.model.user, true).then((p) => {
				setUser(p.userData);
				console.log("user: ", p.userData)
				setProfiled(p.userData.profileComplete);
				getDashboardData();
				getPortfolio(p.userData.id)
				getRecentlyViewed(p.userData.id);
				getClientData(p.userData.id)
			});
		} catch (error) {
			toast.error(error);
		}
	};
	const getClientData = async (id) => {
		let res = await AdminServices.getClient(id).then((res) => JSON.stringify(res));
		res = await JSON.parse(res);
		const aa = JSON.parse(res.data);
		console.log("Get Client API :", aa)
		if (res.type === true) {
			setIsMFDEnabled(aa[0].Client[0].IsMFDEnabled)
			db.set("ClientData", aa[0].Client[0], true)
		}
	};
	const getPortfolio = async (id) => {
		let res = await AdminServices.getPortfolio(id).then((res) => JSON.stringify(res));
		res = await JSON.parse(res);
		const aa = await JSON.parse(res.data);
		console.log("Get portfolio API :", aa)
		if (res.type === true) {
			setportfolio(aa)
		}
	}
	const getDashboardData = async () => {
		let res = await AdminServices.getDashboardData().then((res) => JSON.stringify(res));
		res = JSON.parse(res);
		console.log("Get Dashboard API: ", res.data)
		if (res.type === true) {
			setDashboardContent(res.data)
		}
	}

	const getRecentlyViewed = async (id) => {
		let res = await AdminServices.getRecentlyViewed(id).then((res) => JSON.stringify(res));
		res = JSON.parse(res);
		console.log("Get recently viewed api", res)
		if (res.type === true) {
			setRecentlyViewed(res.data)
		}
	}
