	const NotInvestedSlider = () => {


		const [smallcaseLoginTran, setSmallcaseLoginTran] = React.useState('')
		const [load, seatload] = React.useState(false)
		const [smallLogin, setsmallLogin] = React.useState(false)
		const [smallSignup, setsmallSignup] = React.useState(false)

		const getData = async () => {
			var smallLogin = await localStorage.getItem("smallLogin")
			var smallSignup = await localStorage.getItem("smallSignup")

			if (smallLogin === "Success") {
				setsmallLogin(false)
				setsmallSignup(false)
			} else {

				setsmallLogin(true)
				if (smallSignup === "Success") {
					setsmallSignup(false)
				} else {
					setsmallSignup(true)
				}
			}

		}

		React.useEffect(() => {
			getData()
		})

		const getsmallCaseLoginTransactionId = async () => {
			var data = {
				"intent": "CONNECT"
			}
			var res = await AdminServices.getSmallcaseTransactionId(data).then((rs) => { return rs })
			console.log("New id =>>", res.data.data.transactionId)
			await setSmallcaseLoginTran(res.data.data.transactionId)
			smallcaseLogin(res.data.data.transactionId)
		}

		const smallcaseLogin = async (tranId = smallcaseLoginTran) => {
			await SmallcaseGateway().triggerTransaction(tranId)
				.then((a) => {
					if (a.success) {
						toast.success("Demat connected !! Enjoy investing in Mintbox ");
						setsmallLogin(false)
						setsmallSignup(false)
						var authId = jwt_decode((JSON.parse(a.data)).smallcaseAuthToken).smallcaseAuthId
						localStorage.setItem("smallLogin", "Success")
						localStorage.setItem("smallcase_authId", authId)
					} else {
						toast.error("Demat not connected !! Try again!!");
					}
				})
				.catch((p) => {
					console.log("chandak", p.userInfo)
					if (p.userInfo.errorCode !== 1010 && p.userInfo.errorCode !== 1011) {
						getsmallCaseLoginTransactionId()
					} else {
						// toast.show({
						//     text1: p.message, text2: p.userInfo.errorMessage, type: 'error'
						// });
						toast.error("Demat not connected !! Try again!!");
					}
				});
		}


		const SmallcaseSignup = async () => {
			console.log('\n triggering lead gen');
			try {
				const res = await SmallcaseGateway().signup({
					name: 'Chuck Noris',
					email: 'chuck@galaxy.world',
					contact: '9876543210',
				})
					.then(() => { console.log("signup flow has now ended") });

				console.log('\n lead gen success');
				console.log('\n' + JSON.stringify(res, null, 2));
				if (res.status === "SUBMITTED") {
					setsmallSignup(false)
					localStorage.setItem("smallSignup", "Success")
				}
			} catch (err) {
				console.log('\n error during trigger lead gen' + JSON.stringify(err.userInfo));
			}
		}

		const enableMF = async () => {
			var res = await AdminServices.enableMf(user.email).then((res) => { return res })
			// console.log("resss", res)
		}




		const slides = {
			'content': [
				{
					UID: 'a',
					SubTitle: 'Invest using your demat account',
					Description: 'No sign up required! Just securely link your demat account to start',
					ActionText: 'Link demat account',
					onClick: () => smallcaseLogin(),
					ColorScheme: 'LIGHTBLUE',
					icon: <img src={ManWithCalculator} alt="" />,
					"IsBorderedButton": 0,
					show: smallLogin
				},
				{
					UID: 'b',
					SubTitle: 'Open a demat account',
					Description: 'Dont have a demat account? No worries, we have you covered!',
					ActionText: 'Open demat account',
					onClick: () => SmallcaseSignup(),
					ColorScheme: 'LIGHTBLUE',
					icon: <img src={ManWithCalculator} alt="" />,
					"IsBorderedButton": 1,
					show: smallLogin
				},
				{
					UID: 'c',
					SubTitle: 'Invest in your favourite mutual funds',
					Description: 'Commission free direct mutual funds. No demat account required!',
					ActionText: isMFDEnabled === 0 ? 'Activate mutual funds' : 'Complete profile',
					onClick: () => { isMFDEnabled === 0 ? enableMF() : props.history.push("/complete_pro/pan") },
					ColorScheme: 'LIGHTBLUE',
					icon: <img src={ManWithCalculator} alt="" />,
					"IsBorderedButton": 1,
					show: !user.profileComplete
				}
			]
		}
		return <SliderComponent navigation={props.history} type="boxCard" component={slides} />
	}
