const jwt = require('jsonwebtoken');

function createAuthToken(authId) {
    const secret = "mintbox_cf3c4a2d81944fa1a11de6dc4a3c01da";
    const expiresIn = "1d";
    if (authId) {
        return jwt.sign({
            smallcaseAuthId: authId,
        }, secret, { expiresIn });
    }
    return jwt.sign({
        guest: true,
    }, secret, { expiresIn });
}

