import { ManWithCalculator } from "assets/SVG";
import React from "react";
import cssVar from "views/mobileView/styles";
const SingleContentIconCard = ({ content, navigation, fullWidth = false }) => {
  let backgroundColor = '';
  let buttonColor = '';
  switch (content.ColorScheme) {
    case "BLUE": {
      backgroundColor = "#F3F2FA"
      buttonColor = "#294C88"
      break;
    }
    case "GREEN": {
      backgroundColor = "#C5EED140"
      buttonColor = "#00A6A4"
      break;
    }
    case "YELLOW": {
      backgroundColor = "#FAF8F5"
      buttonColor = "#294C88"
      break;
    }
    case "PINK": {
      backgroundColor = "#F8EBEC"
      buttonColor = "#294C88"
      break;
    }
    case "LIGHTBLUE": {
      backgroundColor = "#F4F8FF"
      buttonColor = "#294C88"
      break;
    }
  }
  return (

    <div
      style={{
        padding: "4%",
        margin: fullWidth ? "4% 0" : "4%",
        borderRadius: fullWidth ? 0 : 6,
        backgroundColor: backgroundColor,
        border: fullWidth ? "none" : "1px solid " + cssVar.getPropertyValue("--divider")
      }}
    >
      <div style={{ color: "#333333", fontSize: "16px", fontWeight: "500", marginBottom: "4%" }}>
        {content.SubTitle}
      </div>
      <p style={{ fontSize: cssVar.getPropertyValue('--font-helper'), fontWeight: "400", color: cssVar.getPropertyValue("--textLight"), marginBottom: 0, width: "90%" }}>
        {content.Description}
      </p>
      <div style={{ display: "flex" }}>
        <div style={{ flex: 7, display: "flex", alignItems: "flex-end" }}>
          <button
            style={{
              borderWidth: "1px",
              borderRadius: 4,
              borderColor: buttonColor,
              fontSize: "14px",
              backgroundColor: content.IsBorderedButton ? "#FFFFFF" : buttonColor,
              color: content.IsBorderedButton ? buttonColor : "#FFFFFF",
              width: "95%",
              height: "44px",
            }}
            onClick={() => {
              content.onClick && content.onClick();
              content.ActionTarget !== '' && navigation.push(content.ActionTarget)
            }}
          >
            {content.ActionText}
          </button>
        </div>
        <div
          style={{
            flex: 3,
            display: "flex",
            alignItems: "flex-end",
          }}
        >
          <img src={ManWithCalculator} alt="" />
        </div>
      </div>
    </div>
  );
};
export default SingleContentIconCard;
