import React from 'react';
import { Button, Spinner } from 'react-bootstrap';
import { AiOutlineRight } from 'react-icons/ai';
import { toast } from 'react-toastify';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import { emailValidator } from "../../../../../libs/validators";
import AdminServices from '../../../../../services/api/AdminServices';
import AuthHeader from '../../../components/molecules/Headers/AuthHeader';
import { browserName, deviceType, mobileModel, osName, osVersion } from 'react-device-detect';
import db from 'libs/db';
export default function OtherDetail(props) {

    const [email, setEmail] = React.useState('');
    const [mobile, setMobile] = React.useState('');
    const [verify, setVerify] = React.useState('');
    const [loading, setloading] = React.useState(true);
	const [nextbtnPressed, setnextbtnPressed] = React.useState(false)
    const { state } = props.location;
    // const state = { name: "omkar", mobile:"9768928535",verify:"MOBILE", otp: "123456" }
    // const state = { name: "omkar", email: "obhogle@gmail.com", verify: "EMAIL", otp: "123456" }

    React.useEffect(() => {
        if (state === null || state === undefined) {
            props.history.push('/signup')
        }
        else {
            setVerify(state.verify === 'EMAIL' ? 'MOBILE' : 'EMAIL')
            setloading(false)
        }
    }, [])

    const validateInput = () => {
        if (verify == 'EMAIL') {
            return emailValidator(email).isValid && !nextbtnPressed
        }
        else { return mobile.length === 10 && !nextbtnPressed }
    }

    const onNextBtnPressed = async () => {
        if(verify==='EMAIL'){
            props.history.push({
                pathname: '/verify_otp2',
                state: {
                    ...state,
                    email,
                    verify,
                }
            })
        }
        else {
            props.history.push({
                pathname: '/verify_otp2',
                state: {
                    ...state,
                    mobile,
                    verify,
                }
            })
        }
                
    };

    const createUser = async ()=> {
		const data = {
            email:state.email,
            clientName:state.name, 
            source: deviceType + ":" + browserName + ":" + (mobileModel === "none" ? osName : mobileModel) + ":" + osVersion
        }
        console.log(data)
		try {
			const res = await AdminServices.createUser(data).then((res) => res)
			if (res.type) {
				if (res.data.Result === 'success') {
					console.log("user created")
                    const userData = {
						name: state.name,
						email: state.email,
						id: res.data.ClientId,
						isLogged: true,
						profileComplete: false,
					};
					db.set(db.model.user, JSON.stringify({ userData }), true);
                    props.history.push({
                        pathname: '/dashboard',
                        state:{...state}
                    })
				} else {
					toast.error("Try again ...");
				}
			} else {
				toast.error("Try again ...");
			}
		} catch (error) {
			toast.error("Try again ...");
		}
	}

    if (loading) {
        return (
            <div className="center-screen">
                <Spinner
                    as="span"
                    animation="grow"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                />
            </div>
        );
    } else {
        return <div className="copycontainer">
            {
                verify === 'EMAIL'?
                <AuthHeader navigation={props.history} left='login'/>
                :
                <AuthHeader navigation={props.history} left='login' right="skip" onSkip={createUser}/>
            }
            <div className="container">
            <h3 className="textHeadauth">{verify === 'EMAIL' ? "Lets verify your email address" : "Lets verify your mobile number"}</h3>
            {verify === 'EMAIL' ?
                <p className='textNormal'>
                    Please enter your email address<br/> No spam ever!
                </p>
                : 
                <p className='textNormal'>
                    Please enter your 10 digit Indian mobile number <br/> No unsolicited calls ever!
                </p>
            }
            <div className="floating-form">
                {verify === 'EMAIL' ?
                    <FloatingTextInput
                        type="email"
                        label="Email address"
                        autoFocus
                        onChange={(p) => setEmail(p.target.value)}
                        value={email}
                    /> :
                    <FloatingTextInput
                        type="tel"
                        label="Mobile number"
                        autoFocus
                        id="Mobile-number"
                        placeholder=" "
                        maxLength={10}
                        value={mobile}
                        onChange={(num) => {
                            const n = num.target.value.replace(/\D/g, '');
                            setMobile(n);
                        }}
                    />
                }
            </div>
            <div className="footer">
                <Button style={{ opacity: validateInput() ? 1 : 0.4 }}
                    onClick={() => { validateInput() && onNextBtnPressed() }}
                    // onClick={()=>props.history.push({
                    //     pathname: '/verify_otp2'
                    //   })}
                    variant="primary" className="btn-footer">
                    Get OTP <AiOutlineRight size={14} />
                </Button>
            </div>
        </div>
        </div>
    }
}
