import React from 'react'
import { toast } from "react-toastify";
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader'
import { FiMinusCircle, FiSearch } from 'react-icons/fi'
import cssVar from 'views/mobileView/styles'
import db from 'libs/db'
import AdminServices from 'services/api/AdminServices';
import { MintboxSmallCube } from "assets/SVG";
import { BsPlusCircle } from 'react-icons/bs';
import { setUserData } from 'redux/CompleteProfile/actions';
import { RiRepeatFill } from 'react-icons/ri';
import SearchBar from 'views/mobileView/components/atoms/SearchBar';
import { setCreateMintboxData } from 'redux/CreateMintbox/actions';

const ReplaceETF = (props) => {
    const [loading, setloading] = React.useState(false);

    const [selectedETFs, setSelectedETFs] = React.useState([]);
    const [allETFs, setAllETFs] = React.useState([]);
    const fundToReplace = props.location.state?.fundToReplace;
    React.useEffect(async () => {
        await getSelectedETFs();
        getAllETFs();
    }, []);

    const getSelectedETFs = async () => {
        try {
            await db.get(db.model.createMintbox).then((u) => {
                if (u?.selectedETFs) {
                    setSelectedETFs(u.selectedETFs)
                }
            })
        } catch (error) {
            toast.error(error);
        }
    };

    const saveData = async (arr) => {
        const dataFinal = {
            selectedETFs: arr
        };
        await db.get(db.model.createMintbox).then((u) => {
            if (u) { Object.assign(u, dataFinal) }
            else { u = dataFinal }
            setCreateMintboxData(u);
        })
    }

    const getAllETFs = async () => {
        try {
            console.log("start")
            await db.get(db.model.createMintbox).then(async (u) => {
                console.log("u",u)
                if (u?.allETFs) {
                    console.log("all etf")
                    setAllETFs(u.allETFs)
                } else {
                    let res = await AdminServices.getAllETFs().then((res) => JSON.stringify(res));
                    res = await JSON.parse(res);
                    if (res.type === true) {
                        console.log("Get All ETFs API: ", res.data)
                        let newArr = res.data.map(fund => { return { ...fund, weight: '0', isLocked: false } })
                        const dataFinal = {
                            'allETFs': newArr
                        };
                        console.log(dataFinal)
                        if (u) { Object.assign(u, dataFinal) }
                        else { u = dataFinal }
                        setCreateMintboxData(u);
                        setAllETFs(newArr)
                    }
                }
            })
        } catch (error) {
            toast.error(error);
        }
    }

    const onPlusIconClick = (id) => {
        const newArr = selectedETFs.map((fund)=>{
            if(fund.UID === fundToReplace.UID){ 
                return {...allETFs.find(f=>f.UID === id),weight:fundToReplace.weight,isLocked:fundToReplace.isLocked}
            }
            else return fund
        })
        saveData(newArr);
        props.history.goBack();
    }
    
    return (
        <div>
            <MainHeader
                header="Build your own mintbox"
                right="help"
                screenType="Invest"
                navigation={props.history}
            />
            <div className="containerData px-0 pb-4" style={{ marginBottom: "140px" }}>
                <div style={{ margin: "4%", fontSize: "14px", fontWeight: "400", color: "#333333", display: "flex", alignItems: "center",gap:"12px" }}>
                    <RiRepeatFill size={16}/>
                    <span style={{ flex: 1 }}>Replacing <b>'{fundToReplace.ETFName}'</b></span>
                </div>
                <div style={{ margin: "4%", fontSize: "14px", fontWeight: "400", color: "#333333"}}>Changed your mind?
                    <span style={{fontWeight: "500", color: "#00A6A4"}} onClick={()=>props.history.goBack()}> Continue building mintbox &gt;</span>
                </div>
                <SearchBar type="ETF" allFunds={allETFs.filter(f=>!selectedETFs.some(x=>x.UID===f.UID))} showPlusIcon={true} onPlusIconClick={onPlusIconClick} />
                
            </div>
        </div>
    )
}

export default ReplaceETF
