import React from 'react';
import { Button, Modal, Spinner } from 'react-bootstrap';
import { FiInfo } from 'react-icons/fi';
import colorsFinal from 'theme/colors';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import ProfileCompleteHeader from 'views/mobileView/components/molecules/Headers/ProfileCompletionHeader';
import cssVar from 'views/mobileView/styles';
import db from 'libs/db';
import BottomBackNextBtn from 'views/mobileView/components/atoms/BottomBackNextBtn';
import DOBInputMask from 'views/mobileView/components/atoms/DOBInputMask';
export default function PersonalDetails(props) {

    const [msg, setmsg] = React.useState('');
    const [name, setname] = React.useState('');
    const [phone, setphone] = React.useState('');
    const [dob, setdob] = React.useState('');
    const [dobv, setdobv] = React.useState('');

    const [msgColor, setmsgColor] = React.useState('');
    const [showmsg, setshowmsg] = React.useState(false);
    const [loading, setloading] = React.useState(true);
    const [nextdisabled, setnextDisabled] = React.useState(true);

    const [value, setValue] = React.useState('');
    const containerStyle = { backgroundColor: 'white', padding: 20 };

    const [savedData, setsavedData] = React.useState();

    // dialog
    const [dshow, setdshow] = React.useState(false);
    const [ddata, setddata] = React.useState([]);
    const [dtitle, setdtitle] = React.useState('');
    const [dimg, setdimg] = React.useState('');

    const [isDatePickerVisible, setDatePickerVisibility] = React.useState(false);

    const showDatePicker = () => {
        setDatePickerVisibility(true);
    };

    const hideDatePicker = () => {
        setDatePickerVisibility(false);
    };

    const handleConfirm = (date) => {
        setdob(date);
        hideDatePicker();
    };

    const dataAdult = {
        head: 'Personal Details'
    };

    const dataMinor = {
        head: 'Minor details'
    };

    const dataNri = {
        head: 'Personal Details'
    };
    const checkValidation = () => {
        if (props.location.state.selection === 'adult') {
            if (name.length > 2 && value.length > 0 && dob !== '' && dobv === false && dob.length === 14) {
                setnextDisabled(false);
            } else {
                setnextDisabled(true);
            }
        } else if (props.location.state.selection === 'nri') {
            if (
                name.length > 2
                && value.length > 0
                && dob !== '' && dobv === false
                && phone.length === 10 && dob.length === 14
            ) {
                setnextDisabled(false);
            } else {
                setnextDisabled(true);
            }
        } else if (name.length > 2 && value.length > 0 && dob !== '' && dobv === false && dob.length === 14) {
            setnextDisabled(false);
        } else {
            setnextDisabled(true);
        }
    };

    React.useEffect(() => {
        checkValidation();
    });

    React.useEffect(() => {
        db.get(db.model.userData).then((u) => {
            console.log(u)
            if (u) {
                if (u.user) {
                    setname(u.user.name || u.panData.panName || '');
                    setdob(u.user.dob || '');
                    setphone(u.user.phone || '');
                    setValue(u.user.gender || '');
                }
            }
            setloading(false);
        });
        checkValidation();
    }, []);

    const data = [
        { key: '0', gender: 'Male' },
        { key: '1', gender: 'Female' }
    ]

    if (loading) {
        return (
            <div className="center-screen">
                <Spinner
                    as="span"
                    animation="grow"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                />
            </div>
        );
    } else {
        return <div className="container">
            {/* <ProfileCompleteHeader
                header={props.location.state.selection === 'nri'
                    ? dataNri.head
                    : props.location.state.selection === 'adult'
                        ? dataAdult.head
                        : dataMinor.head
                }
                navigation={props.history}
                percentage={20} /> */}
            <Modal
                show={dshow}
                onHide={() => setdshow(!dshow)}
                backdrop="static"
                keyboard={false}
                size="sm"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Body>
                    <p className="textHead">{ddata[0]}</p>
                    <p className="textNormal">{ddata[1]}</p>
                </Modal.Body>
                <Button variant="light" style={{ color: colorsFinal.brandPrimary }} onClick={() => setdshow(!dshow)}>
                    Okay
                </Button>
            </Modal>
            <div className="row">
                <div className="col-6">
                    <div className="mt-4 mb-2">

                        <div className="floating-form">
                            <FloatingTextInput
                                className="capitalize"
                                type="text"
                                label="Full name (as per PAN)"
                                id="Full-name"
                                placeholder=" "
                                value={name}
                                onChange={(text) => {
                                    const newName = text.target.value.replace(/[^a-zA-Z\s]/g, '');
                                    setname(newName);
                                    console.log(newName)
                                }}
                                right={<FiInfo
                                    size={15}
                                    style={{ top: "125px" }}
                                    onClick={() => {
                                        setddata(["Please enter your full name as on your PAN card. See example below", ""]);
                                        setdshow(true);
                                    }}
                                />}
                            />
                            <div className="floating-label">
                                <DOBInputMask
                                    dateCheck
                                    onBlur={() => console.log("hey")}
                                    defaultVal={dob}
                                    dobof={props.location.state.selection}
                                    minAge={props.location.state.selection === 'minor' ? 1 : 19}
                                    maxAge={props.location.state.selection === 'minor' ? 18 : 200}
                                    dateValidation={(dobvv) => {
                                        setdobv(dobvv)
                                        // console.log(dobvv)
                                    }}
                                    setDOB={(dobb) => {
                                        setdob(dobb);
                                        console.log(dobb)
                                    }}
                                />
                            </div>
                        </div>
                        <p style={{ fontSize: cssVar.getPropertyValue('--font-authHead') }} >Select your gender</p>
                        {data.map((obj) => (
                            <div key={obj.key} style={{ display: 'flex', flexDirection: 'row' }}>
                                <div className="radio-item">
                                    <input
                                        type="radio"
                                        id={obj.key}
                                        name="ritem"
                                        value={obj.key}
                                        onChange={(val) => {
                                            setValue(val.target.value)
                                            console.log(val.target.value)
                                        }}
                                        className="textNormal"
                                        checked={value === obj.key}
                                    />
                                    <label className="textNormal radio-label" htmlFor={obj.key}>{obj.gender}</label>
                                </div>
                            </div>
                        ))}

                        {props.location.state.selection === 'nri' ? (
                            <FloatingTextInput
                                type="number"
                                label="Indian phone number"
                                id="Phone-number"
                                placeholder=" "
                                autoCapitalize="words"
                                maxLength={10}
                                value={phone}
                                onChange={(num) => {
                                    const newPhone = num.target.value.replace(/\D/g, '');
                                    setphone(newPhone);
                                    console.log(num.target.value)
                                }}
                                right={<FiInfo
                                    size={15}
                                    style={{ top: "125px" }}
                                    onClick={() => {
                                        setddata(["", <span>In case you don't have an Indian phone number, you may use a phone number of a friend/relative in India. Alternatively, you may use MintBox's number <span onClick={() => { setphone('9082913729'); setdshow(false); }} style={{ color: cssVar.getPropertyValue('--primary') }}> 9082913729 </span> . This is a required field by BSE STAR MF - our backend partner. But there is very little use of it for NRI investors</span>]);
                                        setdshow(true);
                                    }}
                                />}
                            />
                        ) : null}
                    </div>
                    <div>
                        <BottomBackNextBtn
                            navigation={props.history}
                            next={
                                props.location.state.selection === 'minor' ? '/admin/client/complete-pro/guardian-pan' : props.location.state.selection === 'nri' ? '/admin/client/complete-pro/faddress' : '/admin/client/complete-pro/address'
                            }
                            params={props.location.state}
                            saveData={{
                                user: {
                                    name,
                                    phone,
                                    dob,
                                    gender: value
                                }
                            }}
                            nextdisabled={nextdisabled} />
                    </div>
                </div>
                <div className="col-6"></div>
            </div>
        </div>
    }
}

