import React from 'react';
import { Button, Modal, Spinner } from 'react-bootstrap';
import AdminServices from 'services/api/AdminServices';
import colorsFinal from 'theme/colors';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import ProfileCompleteHeader from 'views/mobileView/components/molecules/Headers/ProfileCompletionHeader';
import cssVar from 'views/mobileView/styles';
import db from 'libs/db';
import { dropdownCustom } from 'views/mobileView/components/atoms/DataCustomizer';
import CustomDropDown from 'views/mobileView/components/molecules/DropDown/CustomDropDown';
import BottomBackNextBtn from 'views/mobileView/components/atoms/BottomBackNextBtn';


export default function Address(props) {

    const [stateList, setstateList] = React.useState([]);

    const [line1, setLine1] = React.useState('');
    const [line2, setLine2] = React.useState('');
    const [pinCode, setpinCode] = React.useState('');
    const [city, setCity] = React.useState('');
    const [state, setstate] = React.useState({});
    const [selectedItem, setselectedItem] = React.useState([]);

    const [loading, setloading] = React.useState(true);
    const [nextdisabled, setnextDisabled] = React.useState(true);
    const [value, setValue] = React.useState('');

    // helper
    const [helperType, sethelperType] = React.useState('');
    const [helperColor, sethelperColor] = React.useState('');
    const [helperText, sethelperText] = React.useState('');

    const checkOnBlur = (data) => {
        if (data.length !== 6 || isNaN(data)) {
            sethelperType('pin');
            sethelperColor('red');
            sethelperText('Enter a valid pin code');
        } else {
            sethelperType('');
            sethelperColor('');
            sethelperText('');
        }
    };

    const dataAdult = {
        head: 'Address'
    };

    const dataMinor = {
        head: 'Postal Address'
    };

    const dataNri = {
        head: 'Indian Address'
    };

    const checkValidation = () => {
        if (
            line1.length !== 0
            && pinCode.length === 6
            && city.length !== 0
            && Object.entries(state).length !== 0
        ) {
            setnextDisabled(false);
        } else {
            setnextDisabled(true);
        }
    };

    const getData = async () => {
        const res = await AdminServices.getStates().then((res) => res);
        // console.log('DATA : ', res);
        const data = await dropdownCustom(res.data, ['UID', 'StateName']);
        //console.log(data);
        setstateList(data);
    };

    React.useEffect(() => {
        checkValidation();
    });

    React.useEffect(() => {
        db.get(db.model.userData).then((u) => {
            if (u) {
                // u = JSON.parse(u);
                console.log(u)
                if (u.indAdd) {
                    setLine1(u.indAdd.line1 || '');
                    setLine2(u.indAdd.line2 || '');
                    setpinCode(u.indAdd.pincode || '');
                    setCity(u.indAdd.city || '');
                    setstate(u.indAdd.state || '');
                    setstateList(u.indAdd.allstate || []);
                    setloading(false);
                } else {
                    getData().then(() => {
                        setloading(false);
                    });
                }
            }
        });

        checkValidation();
    }, []);

    if (loading) {
        return (
            <div className="center-screen">
                <Spinner
                    as="span"
                    animation="grow"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                />
            </div>
        );
    } else {
        return <div className="container">
            {/* {props.location.state.selection === 'adult' ? (
                <ProfileCompleteHeader
                    header={dataAdult.head}
                    percentage={30}
                    navigation={props.history} />
            ) : props.location.state.selection === 'minor' ? (
                <ProfileCompleteHeader
                    header={dataMinor.head}
                    percentage={30}
                    navigation={props.history} />
            ) : (
                <ProfileCompleteHeader
                    header={dataNri.head}
                    percentage={30}
                    navigation={props.history} />
            )} */}
            <div className="row">
                <div className="col-6">
                    <div className="mt-4 mb-2">

                        <div className="floating-form">
                            <FloatingTextInput
                                type="text"
                                id="line1"
                                label="Line 1"
                                onChange={(val) => {
                                    setLine1(val.target.value)
                                }}
                                onBlur={() => {
                                    if (line1.length === 0) {
                                        sethelperType('line1');
                                        sethelperColor('red');
                                        sethelperText('Line 1 can\'t be empty');
                                    } else {
                                        sethelperType('');
                                        sethelperColor('');
                                        sethelperText('');
                                    }
                                }}
                                autoCapitalize="words"
                                value={line1}
                            />
                            {helperType === 'line1' ? (
                                <span style={{ fontSize: cssVar.getPropertyValue('--font-helper'), color: helperColor }}>
                                    {helperText}
                                </span>
                            ) : null}

                            <FloatingTextInput
                                type="text"
                                id="line2"
                                label="Line 2"
                                autoCapitalize="words"
                                value={line2}
                                onChange={(val) => {
                                    setLine2(val.target.value)
                                    console.log(val.target.value)
                                }}
                            />

                            <FloatingTextInput
                                type="tel"
                                id="pin"
                                label="PIN code"
                                maxLength="6"
                                // error={helperType === 'pin'}
                                value={pinCode}
                                onBlur={() => checkOnBlur(pinCode)}
                                onChange={(val) => {
                                    setpinCode(val.target.value)
                                    console.log(val.target.value)
                                }}
                            />
                            {helperType === 'pin' ? (
                                <span style={{ fontSize: cssVar.getPropertyValue('--font-helper'), color: helperColor }}>
                                    {helperText}
                                </span>
                            ) : null}

                            <FloatingTextInput
                                type="text"
                                id="city"
                                label="City"
                                onChange={(val) => {
                                    setCity(val.target.value)
                                    console.log(val.target.value)
                                }}
                                onBlur={() => {
                                    if (city.length === 0) {
                                        sethelperType('city');
                                        sethelperColor('red');
                                        sethelperText('city can\'t be empty');
                                    } else {
                                        sethelperType('');
                                        sethelperColor('');
                                        sethelperText('');
                                    }
                                }}
                                autoCapitalize="words"
                                value={city}
                            />
                            {helperType === 'city' ? (
                                <span style={{ fontSize: cssVar.getPropertyValue('--font-helper'), color: helperColor }}>
                                    {helperText}
                                </span>
                            ) : null}

                            <CustomDropDown
                                placeholder="Enter state name"
                                label="State"
                                initial={state}
                                items={stateList}
                                onChange={(item) => setstate(item)} />
                        </div>
                    </div>
                    <div>
                        {props.location.state.selection === 'adult' ? (
                            <BottomBackNextBtn
                                navigation={props.history}
                                next="/admin/client/complete-pro/fatca"
                                params={props.location.state}
                                saveData={{
                                    indAdd: {
                                        line1,
                                        line2,
                                        pincode: pinCode,
                                        city,
                                        state,
                                        allstate: stateList,
                                        loadData: false
                                    }
                                }}
                                nextdisabled={nextdisabled} />
                        ) : props.location.state.selection === 'minor' ? (
                            <BottomBackNextBtn
                                navigation={props.history}
                                next="/admin/client/complete-pro/fatca"
                                params={props.location.state}
                                saveData={{
                                    indAdd: {
                                        line1,
                                        line2,
                                        pincode: pinCode,
                                        city,
                                        state,
                                        allstate: stateList,
                                        loadData: false
                                    }
                                }}
                                nextdisabled={nextdisabled} />
                        ) : (
                            <BottomBackNextBtn
                                navigation={props.history}
                                next="/admin/client/complete-pro/faddress"
                                params={props.location.state}
                                saveData={{
                                    indAdd: {
                                        line1,
                                        line2,
                                        pincode: pinCode,
                                        city,
                                        state,
                                        allstate: stateList,
                                        loadData: false
                                    }
                                }}
                                nextdisabled={props.location.state.selection === 'nri' ? false : nextdisabled} />
                        )}
                    </div>
                </div>
                <div className="col-6"></div>
            </div>
        </div>
    }
}

