import React from 'react';
import { Button, Modal, Spinner } from 'react-bootstrap';
import { toast } from 'react-toastify';
import { FiInfo } from 'react-icons/fi';
import colorsFinal from 'theme/colors';

import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import { emailValidator } from 'libs/validators';
import AdminServices from 'services/api/AdminServices';
import db from 'libs/db';
import { setUserData } from 'redux/CompleteProfile/actions';
import DOBInputMask from 'views/mobileView/components/atoms/DOBInputMask';
import cssVar from 'views/mobileView/styles';
import CustomDropDown from 'views/mobileView/components/molecules/DropDown/CustomDropDown';
import { dropdownCustom } from 'views/mobileView/components/atoms/DataCustomizer';
import BottomBackNextBtn from 'views/mobileView/components/atoms/BottomBackNextBtn';

export default function CompleteProfilePage1(props) {

    const [name, setName] = React.useState('');
    const [email, setEmail] = React.useState('');
    const [phone, setPhone] = React.useState('');
    const [dob, setDob] = React.useState('');
    const [dobv, setdobv] = React.useState('');
    const [gender, setGender] = React.useState('');
    const [taxStatus, setTaxStatus] = React.useState('');
    const [panNumber, setPanNumber] = React.useState('');
    const [indianPhone, setIndianPhone] = React.useState('')
    const [guardianName, setGuardianName] = React.useState('')
    const [guardianDob, setGuardianDob] = React.useState('')
    const [line1, setLine1] = React.useState('');
    const [line2, setLine2] = React.useState('');
    const [pinCode, setPinCode] = React.useState('');
    const [city, setCity] = React.useState('');
    const [state, setState] = React.useState({});
    const [stateList, setStateList] = React.useState({});
    const [country, setCountry] = React.useState('');
    const [countryList, setCountryList] = React.useState([]);

    const [loading, setloading] = React.useState(true);
    const [helperType, sethelperType] = React.useState('');
    const [helperColor, sethelperColor] = React.useState('');
    const [helperText, sethelperText] = React.useState('');
    const [dshow, setdshow] = React.useState(false);
    const [ddata, setddata] = React.useState([]);
    const genderDataArray = [
        { key: 'm', gender: 'Male' },
        { key: 'f', gender: 'Female' }
    ]
    const taxStatusDataArray = [
        {
            key: '0',
            data: 'Indian resident (adult)',
            showInfo: false
        },
        {
            key: '1',
            data: 'Indian resident (minor)',
            showInfo: false
        },
        {
            key: '2',
            data: 'Non resident Indian',
            showInfo: true
        }
    ];

    const getData = async () => {
        const res = await AdminServices.getStates().then((res) => res);
        const data = await dropdownCustom(res.data, ['UID', 'StateName']);
        setStateList(data);
        const res2 = await AdminServices.getCountries().then((res) => res);
        const data2 = await dropdownCustom(res2.data, ['UID', 'Country']);
        setCountryList(data2);
    };
    const nextValidate = () => {
        // if ((value === '0' || value === '2') && showdata) {
        //     return true;
        // } if (value === '1') {
        //     return true;
        // }
        return true;
    };

    const onNextPress = () => {
        const dataFinal = {
            user: {
                name,
                email,
                phone,
                dob,
                gender,
                indianPhone: taxStatus === '2' ? indianPhone : '',
            },
            panData: {
                taxStatus: taxStatus,
                pan: taxStatus === '1' ? '' : panNumber,
            },
            legalGuard: {
                name: guardianName,
                dob: guardianDob
            },
            indAdd: {
                line1,
                line2,
                pincode: pinCode,
                city,
                state,
                allstate: stateList,
                country,
                allcountry: countryList,
            }
        };

        db.get(db.model.userData).then((u) => {
            if (u) {
                Object.assign(u, dataFinal);
            } else {
                u = dataFinal
            }
            setUserData(u).then(() => {
                props.history.push({
                    pathname: '/admin/client/complete-pro/add-a-client/2',
                    state: { taxStatus }
                })
            })
        });
    };
    React.useEffect(() => {
        db.get(db.model.userData).then((u) => {
            if (!u.indAdd?.allstate && !u.indAdd?.allcountry) {
                getData().then(() => {
                    setloading(false);
                });
            }
            else { setloading(false); }
            if (u) {
                setName(u.user?.name || '');
                setEmail(u.user?.email || '');
                setPhone(u.user?.phone || '');
                setDob(u.user?.dob || '');
                setGender(u.user?.gender || '');
                setIndianPhone(u.user?.indianPhone || '');
                setTaxStatus(u.panData?.taxStatus || '')
                setPanNumber(u.panData?.panNumber || '')
                setGuardianName(u.legalGuard?.guardianName || '')
                setGuardianDob(u.legalGuard?.guardianDob || '')
                setLine1(u.indAdd?.line1 || '');
                setLine2(u.indAdd?.line2 || '');
                setPinCode(u.indAdd?.pincode || '');
                setCity(u.indAdd?.city || '');
                setState(u.indAdd?.state || '')
                setCountry(u.indAdd?.country || '')
                setStateList(u.indAdd?.allstate || []);
                setCountryList(u.indAdd?.allcountry || []);
                setloading(false);
            }
        });
    }, []);


    if (loading) {
        return (
            <div className="center-screen">
                <Spinner
                    as="span"
                    animation="grow"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                />
            </div>
        );
    } else {
        return <div className="container">
            <Modal
                show={dshow}
                onHide={() => setdshow(!dshow)}
                backdrop="static"
                keyboard={false}
                size="sm"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Body>
                    <p className="textHead">{ddata[0]}</p>
                    <p className="textNormal">{ddata[1]}</p>
                </Modal.Body>
                <Button variant="light" style={{ color: colorsFinal.brandPrimary }} onClick={() => setdshow(!dshow)}>
                    Okay
                </Button>
            </Modal>
            <div className="floating-form">
                <div className="row">
                    <div className="col-6">
                        <FloatingTextInput
                            className="capitalize"
                            type="text"
                            label="Full name (as per PAN)"
                            id="Full-name"
                            placeholder=" "
                            value={name}
                            height="2.5rem"
                            onChange={(text) => {
                                const newName = text.target.value.replace(/[^a-zA-Z\s]/g, '');
                                setName(newName);
                                console.log(newName)
                            }}
                            right={<FiInfo
                                size={15}
                                style={{ top: "125px" }}
                                onClick={() => {
                                    setddata(["Please enter your full name as on your PAN card. See example below", ""]);
                                    setdshow(true);
                                }}
                            />}
                        />
                    </div>
                    <div className="col-6">
                        <FloatingTextInput
                            label="Email"
                            type="email"
                            height="2.5rem"
                            onChange={(p) => setEmail(p.target.value)}
                            value={email}
                        />
                    </div>
                </div>
                <div className="row">
                    <div className="col-6">
                        <FloatingTextInput
                            label="Phone Number"
                            type="tel"
                            maxLength={10}
                            height="2.5rem"
                            onChange={(num) => {
                                const newPhone = num.target.value.replace(/\D/g, '');
                                setPhone(newPhone);
                            }}
                            value={phone}
                        />
                    </div>
                    <div className="col-6">
                        <div className="floating-label">
                            <DOBInputMask
                                dateCheck
                                onBlur={() => console.log("hey")}
                                defaultVal={dob}
                                dobof={taxStatus}
                                height="2.5rem"
                                minAge={'taxStatus' === 'minor' ? 1 : 19}
                                maxAge={'taxStatus' === 'minor' ? 18 : 200}
                                dateValidation={(dobvv) => {
                                    setdobv(dobvv)
                                }}
                                setDOB={(dobb) => {
                                    setDob(dobb);
                                    console.log(dobb)
                                }}
                            />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-6">
                        <div className="textHelper">
                            <p>Select investor tax status</p>
                            {taxStatusDataArray.map((obj) => (
                                <div
                                    key={obj.key}
                                    style={{
                                        display: 'flex',
                                        flexDirection: 'row'
                                    }}>
                                    <div className="radio-item">
                                        <input
                                            type="radio"
                                            id={obj.key}
                                            name="tax-status"
                                            value={obj.key}
                                            onChange={(val) => {
                                                setTaxStatus(val.target.value)
                                            }}
                                            checked={taxStatus === obj.key}
                                        />
                                        <label className="radio-label" htmlFor={obj.key}>{obj.data}</label>
                                    </div>
                                    {obj.showInfo ? (
                                        <FiInfo
                                            style={{ alignSelf: 'center', marginLeft: 10, marginBottom: 5 }}
                                            size={15}
                                            className="primary"
                                            onClick={() => {
                                                setddata(["", "NRIs can invest via MintBox using NRE or NRO bank accounts only"]);
                                                setdshow(true);
                                            }}
                                        />
                                    ) : null}
                                </div>
                            ))}
                        </div>
                    </div>
                    <div className="col-6">
                        <div className="textHelper">
                            <p>Gender</p>
                            {genderDataArray.map((obj) => (
                                <div key={obj.key} style={{ display: 'flex', flexDirection: 'row' }}>
                                    <div className="radio-item">
                                        <input
                                            type="radio"
                                            id={obj.key}
                                            name="gender"
                                            value={obj.key}
                                            onChange={(val) => {
                                                setGender(val.target.value)
                                                console.log(val.target.value)
                                            }}
                                            checked={gender === obj.key}
                                        />
                                        <label className="radio-label" htmlFor={obj.key}>{obj.gender}</label>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-6">
                        <FloatingTextInput
                            maxLength={10}
                            id="pan"
                            type="text"
                            label={taxStatus === '1' ? "Guardian PAN" : "PAN"}
                            height="2.5rem"
                            onChange={(text) => {
                                setPanNumber(text.target.value);
                                // validatePan(text.target.value);
                            }}
                            value={panNumber}
                            right={<FiInfo
                                size={15}
                                onClick={() => {
                                    setddata(["Your PAN will help us check if you are KYC registered and ready to invest",
                                        "For resident minor account, enter the PAN of the legal guardian"]);
                                    setdshow(true);
                                }}
                            />}
                        />
                    </div>
                    <div className="col-6">
                        {taxStatus === '2' ? (
                            <FloatingTextInput
                                type="number"
                                label="Indian phone number"
                                id="Phone-number"
                                placeholder=" "
                                autoCapitalize="words"
                                maxLength={10}
                                value={indianPhone}
                                height="2.5rem"
                                onChange={(num) => {
                                    const newPhone = num.target.value.replace(/\D/g, '');
                                    setIndianPhone(newPhone);
                                    console.log(num.target.value)
                                }}
                                right={<FiInfo
                                    size={15}
                                    style={{ top: "125px" }}
                                    onClick={() => {
                                        setddata(["", <span>In case you don't have an Indian phone number, you may use a phone number of a friend/relative in India. Alternatively, you may use MintBox's number <span onClick={() => { setIndianPhone('9082913729'); setdshow(false); }} style={{ color: cssVar.getPropertyValue('--primary') }}> 9082913729 </span> . This is a required field by BSE STAR MF - our backend partner. But there is very little use of it for NRI investors</span>]);
                                        setdshow(true);
                                    }}
                                />}
                            />
                        ) : null}
                    </div>
                </div>
                {taxStatus === '1' ?
                    <div className="row">
                        <div className="col-6">
                            <FloatingTextInput
                                className="capitalize"
                                type="text"
                                label="Guardian Name (as per PAN)"
                                id="Full-name"
                                placeholder=" "
                                value={guardianName}
                                height="2.5rem"
                                onChange={(text) => {
                                    const newName = text.target.value.replace(/[^a-zA-Z\s]/g, '');
                                    setGuardianName(newName);
                                    console.log(newName)
                                }}
                            />
                        </div>
                        <div className="col-6">
                            <DOBInputMask
                                dateCheck
                                onBlur={() => console.log("hey")}
                                defaultVal={guardianDob}
                                dobof={'minor'}
                                minAge={19}
                                maxAge={200}
                                height="2.5rem"
                                dateValidation={(dobvv) => {
                                    setdobv(dobvv)
                                    // console.log(dobvv)
                                }}
                                setDOB={(dobb) => {
                                    setGuardianDob(dobb);
                                    console.log(dobb)
                                }}
                            />
                        </div>
                    </div>
                    : null}
                <div className="row">
                    <label style={{ marginTop: "12px" }}>
                        {taxStatus === '0' ? "Address"
                            : taxStatus === "1" ? "Postal Address"
                                : "Foreign Address"}
                    </label>
                    <div className="col-6">
                        <FloatingTextInput
                            type="text"
                            id="line1"
                            label="Line 1"
                            height="2.5rem"
                            onChange={(val) => {
                                setLine1(val.target.value)
                            }}
                            onBlur={() => {
                                if (line1.length === 0) {
                                    sethelperType('line1');
                                    sethelperColor('red');
                                    sethelperText('Line 1 can\'t be empty');
                                } else {
                                    sethelperType('');
                                    sethelperColor('');
                                    sethelperText('');
                                }
                            }}
                            autoCapitalize="words"
                            value={line1}
                        />
                        {helperType === 'line1' ? (
                            <span style={{ fontSize: cssVar.getPropertyValue('--font-helper'), color: helperColor }}>
                                {helperText}
                            </span>
                        ) : null}
                    </div>
                    <div className="col-6">
                        <FloatingTextInput
                            type="text"
                            id="line2"
                            label="Line 2"
                            autoCapitalize="words"
                            value={line2}
                            height="2.5rem"
                            onChange={(val) => {
                                setLine2(val.target.value)
                            }}
                        />
                    </div>
                </div>
                <div className="row">
                    <div className="col-3">
                        <FloatingTextInput
                            type="text"
                            id="city"
                            label="City"
                            height="2.5rem"
                            onChange={(val) => {
                                setCity(val.target.value)
                                console.log(val.target.value)
                            }}
                            onBlur={() => {
                                if (city.length === 0) {
                                    sethelperType('city');
                                    sethelperColor('red');
                                    sethelperText('city can\'t be empty');
                                } else {
                                    sethelperType('');
                                    sethelperColor('');
                                    sethelperText('');
                                }
                            }}
                            autoCapitalize="words"
                            value={city}
                        />
                        {helperType === 'city' ? (
                            <span style={{ fontSize: cssVar.getPropertyValue('--font-helper'), color: helperColor }}>
                                {helperText}
                            </span>
                        ) : null}
                    </div>
                    <div className="col-3">
                        <FloatingTextInput
                            type="tel"
                            id="pin"
                            label={taxStatus === '2' ? "Zip/Postal Code" : "PIN Code"}
                            maxLength="6"
                            height="2.5rem"
                            value={pinCode}
                            onBlur={() => {
                                if (pinCode.length !== 6 || isNaN(pinCode)) {
                                    sethelperType('pin');
                                    sethelperColor('red');
                                    sethelperText('Enter a valid pin code');
                                } else {
                                    sethelperType('');
                                    sethelperColor('');
                                    sethelperText('');
                                }
                            }}
                            onChange={(val) => {
                                setPinCode(val.target.value)
                            }}
                        />
                        {helperType === 'pin' ? (
                            <span style={{ fontSize: cssVar.getPropertyValue('--font-helper'), color: helperColor }}>
                                {helperText}
                            </span>
                        ) : null}
                    </div>
                    <div className="col-6">
                        {taxStatus === '2' ? (
                            <CustomDropDown
                                placeholder="Enter country name"
                                label="Country"
                                initial={country}
                                items={countryList}
                                height="2.5rem"
                                onChange={(item) => setCountry(item)} />
                        ) : (
                            <CustomDropDown
                                placeholder="Enter state name"
                                label="State"
                                initial={state}
                                items={stateList}
                                height="2.5rem"
                                onChange={(item) => setState(item)} />
                        )}

                    </div>
                </div>
            </div>
            <div>
                <Button
                    variant="primary"
                    className="btn-footer"
                    style={{ opacity: nextValidate() ? 1 : 0.4 }}
                    onClick={nextValidate()
                        ? () => onNextPress()
                        : () => { }}
                >
                    Next &#8594;
                </Button>
            </div>
        </div>
    }
}
