// core components
import AdminNavbarLinks from "../../components/Navbars/AdminNavbarLinks.js";
import PropTypes from "prop-types";
import React from "react";
import { NavLink, useLocation } from "react-router-dom";
import { ExpandLess, ExpandMore } from "@material-ui/icons";
import { Collapse } from "@material-ui/core";


const useStyles = makeStyles(styles);

export default function Sidebar(props) {
  const classes = useStyles();
  let location = useLocation();
  // verifies if routeName is the one active (in browser input)
  function activeRoute(routeName) {
    return location.pathname === routeName;
  }
  const { color, logo, image, logoText, routes } = props;


  const [menu, setMenu] = React.useState({});
  const handleClick = (item) => {
    let newData = { [item]: !menu[item] };
    setMenu(newData);
  }

  const handleMenu = (children, level = 0) => {
    return children.map(({ children, name, path, layout, icon, show }) => {
      var activePro = " ";
      var listItemClasses;
      if (path === "/upgrade-to-pro") {
        activePro = classes.activePro + " ";
        listItemClasses = classNames({
          [" " + classes[color]]: true,
        });
      } else {
        listItemClasses = classNames({
          [" " + classes[color]]: activeRoute(layout + path),
        });
      }
      const whiteFontClasses = classNames({
        [" " + classes.whiteFont]: activeRoute(layout + path),
      });

      console.log(layout + path)
      if (show) {
        if (!children) {
          return (
            <NavLink
              to={layout + path}
              className={activePro + classes.item}
              activeClassName="active"
              key={path}
            >
              <ListItem button className={classes.itemLink + listItemClasses}>
                {level === 0 ?
                  <div style={{ display: "flex", flexDirection: "row", alignItems: "center" }}>
                    <Icon
                      className={classNames(classes.itemIcon, whiteFontClasses)}
                    >
                      {icon}
                    </Icon>
                    <ListItemText
                      primary={name}
                      className={classNames(classes.itemText, whiteFontClasses)}
                      disableTypography={true}
                    />
                  </div>
                
                  :
                  <div style={{ display: "flex", flexDirection: "row", alignItems: "center" }}>
                    <Icon
                      className={classNames(classes.itemIcon, whiteFontClasses)}
                    >
                    
                    </Icon>
                    <ListItemText
                      primary={name}
                      className={classNames(classes.itemText, whiteFontClasses)}
                      disableTypography={true}
                    />
                  </div>
                }
             
              

              
              </ListItem>
            </NavLink>
          )
        }
        return (
          <div key={name}>
            <ListItem
              button className={classes.itemLink + listItemClasses}
              key={name}
              onClick={() => handleClick(name)}
            >
              {menu[name] ?
                (
                  <div style={{ display: "flex", flexDirection: "row", alignItems: "center" }}>
                    <Icon
                      className={classNames(classes.itemIcon, whiteFontClasses)}
                    >
                      {icon}
                    </Icon>
                    <ListItemText
                      primary={name}
                      className={classNames(classes.itemText, whiteFontClasses)}
                      disableTypography={true}
                    />
                    <ExpandLess className={classNames(classes.itemText, whiteFontClasses)} />
                  </div>) :

                (
                  <div style={{ display: "flex", flexDirection: "row", alignItems: "center" }}>
                    <Icon
                      className={classNames(classes.itemIcon, whiteFontClasses)}
                    >
                      {icon}
                    </Icon>
                    <ListItemText
                      primary={name}
                      className={classNames(classes.itemText, whiteFontClasses)}
                      disableTypography={true}
                    />
                    <ExpandMore className={classNames(classes.itemText, whiteFontClasses)} />
                  </div>
                )
              }


            </ListItem>
            <Collapse
              in={(menu[name]) ? true : false}
              timeout="auto"
              unmountOnExit
            >
              {handleMenu(children, 1)}
            </Collapse>
          </div>
        )
      }
      })
  
  }



  var links = (
    <List className={classes.list}>

      {handleMenu(routes)}
      {/* {routes.map((prop, key) => {
        var activePro = " ";
        var listItemClasses;
        if (path === "/upgrade-to-pro") {
          activePro = classes.activePro + " ";
          listItemClasses = classNames({
            [" " + classes[color]]: true,
          });
        } else {
          listItemClasses = classNames({
            [" " + classes[color]]: activeRoute(layout + path),
          });
        }
        const whiteFontClasses = classNames({
          [" " + classes.whiteFont]: activeRoute(layout + path),
        });
        return (
          <NavLink
            to={layout + path}
            className={activePro + classes.item}
            activeClassName="active"
            key={key}
          >
            <ListItem button className={classes.itemLink + listItemClasses}>
              {typeof icon === "string" ? (
                <Icon
                  className={classNames(classes.itemIcon, whiteFontClasses, {
                    [classes.itemIconRTL]: props.rtlActive,
                  })}
                >
                  {icon}
                </Icon>
              ) : (
                <icon
                  className={classNames(classes.itemIcon, whiteFontClasses, {
                    [classes.itemIconRTL]: props.rtlActive,
                  })}
                />
              )}
              <ListItemText
                primary={props.rtlActive ? rtlName : name}
                className={classNames(classes.itemText, whiteFontClasses, {
                  [classes.itemTextRTL]: props.rtlActive,
                })}
                disableTypography={true}
              />
            </ListItem>
          </NavLink>
        );
      })} */}
    </List>
  );
  var brand = (
    <div className={classes.logo}>
      <a
        href="https://mintbox.ai/"
        className={classNames(classes.logoLink)}
        target="_blank"
      >
        <div className={classes.logoImage}>
          <img src={logo} alt="logo" className={classes.img} />
        </div>
        {logoText}
      </a>
    </div>
  );
  return (
    <div>
      <Hidden mdUp implementation="css">
        <Drawer
          variant="temporary"
          anchor={props.rtlActive ? "left" : "right"}
          open={props.open}
          classes={{
            paper: classNames(classes.drawerPaper, {
              [classes.drawerPaperRTL]: props.rtlActive,
            }),
          }}
          onClose={props.handleDrawerToggle}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
        >
          {brand}
          <div className={classes.sidebarWrapper}>
            <AdminNavbarLinks />
            {links}
          </div>
          {image !== undefined ? (
            <div
              className={classes.background}
              style={{ backgroundImage: "url(" + image + ")" }}
            />
          ) : null}
        </Drawer>
      </Hidden>
      <Hidden smDown implementation="css">
        <Drawer
          anchor={props.rtlActive ? "right" : "left"}
          variant="permanent"
          open
          classes={{
            paper: classNames(classes.drawerPaper, {
              [classes.drawerPaperRTL]: props.rtlActive,
            }),
          }}
        >
          {brand}

          <div className={classes.sidebarWrapper}>
            {links}
          </div>

          {image !== undefined ? (
            <div
              className={classes.background}
              style={{ backgroundImage: "url(" + image + ")" }}
            />
          ) : null}
        </Drawer>
      </Hidden>
    </div>
  );
}

Sidebar.propTypes = {
  rtlActive: PropTypes.bool,
  handleDrawerToggle: PropTypes.func,
  bgColor: PropTypes.oneOf(["purple", "blue", "green", "orange", "red"]),
  logo: PropTypes.string,
  image: PropTypes.string,
  logoText: PropTypes.string,
  routes: PropTypes.arrayOf(PropTypes.object),
  open: PropTypes.bool,
};
